import { ScaleLoader } from 'react-spinners';


const ScheduleModel = ({
    aliasName,
    modelTitle,
    showModal,
    closeModal,
    handleEveryHourChange,
    selectedeveryhour,
    hours,
    handleEveryMinuteChange,
    selectedeveryminute,
    minutes,
    handleScheduleChange,
    selectedschedule,
    scheduleOptions,
    handleWeekdayChange,
    selectedweekday,
    weekdayOptions,
    handleMonthChange,
    selectedmonth,
    monthOptions,
    handlePastHourChange,
    selectedpasthour,
    pastHourOptions,
    handleHourChange,
    selectedhour,
    hourOptions,
    handleMinuteChange,
    selectedminute,
    minuteOptions,
    handleTimezoneChange,
    selectestimezone,
    timezoneOptions,
    setVisibility,
    ispsLoading,
    saveClick,
    clear
}) => {
    return (
        <div id="Popviewschedulejob" className={`modal fade ${showModal ? 'show' : ''}`} role="dialog" style={{ display: showModal ? 'block' : 'none' }}>
            <div className="modal-dialog" style={{ minWidth: '650px' }}>
                <div className="modal-content">
                    <div className="modal-header">
                        <h4 className="modal-title">{modelTitle}</h4>
                        <button type="button" value="Close" className="btn-close" data-bs-dismiss="modal" data-dismiss="modal" onClick={closeModal}></button>
                    </div>
                    <div className="modal-body">
                        <div id="mybcontainer" className="container-sm border">

                            <div className="row">
                                <div className="col-sm-1">
                                </div>
                                <div className="col-sm-10">
                                    <br />
                                    <div className="input-group input-group-sm">
                                        <div className="form-group form-control-sm mt-1" id={`${aliasName}col_every`} style={{ display: 'none' }} >
                                            <label>Every</label>
                                        </div>
                                        <div className="form-group form-control-sm" id={`${aliasName}col_every_hour`} style={{ display: 'none' }}>
                                            <select className="form-select form-select-sm" id={`${aliasName}dd_every_hour`} onChange={handleEveryHourChange} value={selectedeveryhour} required>
                                                {hours.map((hour) => (
                                                    <option key={hour} value={hour}>
                                                        {hour}
                                                    </option>
                                                ))}
                                            </select>
                                        </div>
                                        <div className="form-group form-control-sm" id={`${aliasName}col_every_minute`} style={{ display: 'none' }}>
                                            <select className="form-select form-select-sm" id={`${aliasName}dd_every_minute`} onChange={handleEveryMinuteChange} value={selectedeveryminute} required>
                                                {minutes.map((minute) => (
                                                    <option key={minute} value={minute}>
                                                        {minute}
                                                    </option>
                                                ))}
                                            </select>
                                        </div>
                                        <div className="form-group form-control-sm" id={`${aliasName}col_schedule`} style={{ display: 'none' }}>
                                            <select className="form-select form-select-sm" id={`${aliasName}dd_schedule`} onChange={handleScheduleChange} required value={selectedschedule}>
                                                {scheduleOptions.map((option) => (
                                                    <option key={option} value={option}>
                                                        {option}
                                                    </option>
                                                ))}
                                            </select>
                                            {selectedeveryminute === "Every 1" && selectedschedule === "Minute" && (
                                                (() => {
                                                    setVisibility(`${aliasName}col_every_minute`, true);
                                                    setVisibility(`${aliasName}col_every`, false);
                                                    setVisibility(`${aliasName}col_schedule`, true);
                                                    setVisibility(`${aliasName}col_hour`, false);
                                                    setVisibility(`${aliasName}col_at`, false);
                                                    setVisibility(`${aliasName}col_colon`, false);
                                                    setVisibility(`${aliasName}col_minute`, false);
                                                    setVisibility(`${aliasName}dvtimezone`, true);
                                                    setVisibility(`${aliasName}col_every_hour`, false);
                                                    setVisibility(`${aliasName}col_past_hour`, false);
                                                    setVisibility(`${aliasName}col_on`, false);
                                                    setVisibility(`${aliasName}col_from`, false);
                                                    setVisibility(`${aliasName}col_weekday`, false);
                                                    setVisibility(`${aliasName}col_onthe`, false);
                                                    setVisibility(`${aliasName}col_month`, false);

                                                })()
                                            )}
                                            {selectedeveryminute !== "Every 1" && selectedschedule === "Minute" && (
                                                (() => {
                                                    setVisibility(`${aliasName}col_every_minute`, true);
                                                    setVisibility(`${aliasName}col_every`, false);
                                                    setVisibility(`${aliasName}col_schedule`, true);
                                                    setVisibility(`${aliasName}col_hour`, false);
                                                    setVisibility(`${aliasName}col_at`, false);
                                                    setVisibility(`${aliasName}col_colon`, false);
                                                    setVisibility(`${aliasName}col_minute`, false);
                                                    setVisibility(`${aliasName}dvtimezone`, true);
                                                    setVisibility(`${aliasName}col_every_hour`, false);
                                                    setVisibility(`${aliasName}col_past_hour`, true);
                                                    setVisibility(`${aliasName}col_on`, false);
                                                    setVisibility(`${aliasName}col_from`, true);
                                                    setVisibility(`${aliasName}col_weekday`, false);
                                                    setVisibility(`${aliasName}col_onthe`, false);
                                                    setVisibility(`${aliasName}col_month`, false);

                                                })()
                                            )}

                                            {selectedeveryhour =="Every 1"  && selectedschedule === "Hour" && (
                                                (() => {
                                                    setVisibility(`${aliasName}col_every_hour`, true);
                                                    setVisibility(`${aliasName}col_past_hour`, true);
                                                    setVisibility(`${aliasName}col_on`, false);
                                                    setVisibility(`${aliasName}col_from`, false);
                                                    setVisibility(`${aliasName}col_weekday`, false);
                                                    setVisibility(`${aliasName}col_onthe`, false);
                                                    setVisibility(`${aliasName}col_every_minute`, false);
                                                    setVisibility(`${aliasName}col_every`, false);
                                                    setVisibility(`${aliasName}col_schedule`, true);
                                                    setVisibility(`${aliasName}col_hour`, false);
                                                    setVisibility(`${aliasName}col_at`, true);
                                                    setVisibility(`${aliasName}col_colon`, false);
                                                    setVisibility(`${aliasName}col_minute`, false);
                                                    setVisibility(`${aliasName}dvtimezone`, true);
                                                    setVisibility(`${aliasName}col_month`, false);
                                                })()
                                            )}
                                             {selectedeveryhour !="Every 1" && selectedschedule === "Hour" && (
                                                (() => {
                                                    setVisibility(`${aliasName}col_every_hour`, true);
                                                    setVisibility(`${aliasName}col_past_hour`, false);
                                                    setVisibility(`${aliasName}col_on`, false);
                                                    setVisibility(`${aliasName}col_from`, true);
                                                    setVisibility(`${aliasName}col_weekday`, false);
                                                    setVisibility(`${aliasName}col_onthe`, false);
                                                    setVisibility(`${aliasName}col_every_minute`, false);
                                                    setVisibility(`${aliasName}col_every`, false);
                                                    setVisibility(`${aliasName}col_schedule`, true);
                                                    setVisibility(`${aliasName}col_hour`, true);
                                                    setVisibility(`${aliasName}col_at`, false);
                                                    setVisibility(`${aliasName}col_colon`, false);
                                                    setVisibility(`${aliasName}col_minute`, true);
                                                    setVisibility(`${aliasName}dvtimezone`, true);
                                                    setVisibility(`${aliasName}col_month`, false);
                                                })()
                                            )}

                                            {selectedschedule === "Day" && (
                                                (() => {
                                                    setVisibility(`${aliasName}col_every`, true);
                                                    setVisibility(`${aliasName}col_schedule`, true);
                                                    setVisibility(`${aliasName}col_hour`, true);
                                                    setVisibility(`${aliasName}col_at`, true);
                                                    setVisibility(`${aliasName}col_colon`, true);
                                                    setVisibility(`${aliasName}col_minute`, true);
                                                    setVisibility(`${aliasName}dvtimezone`, true);
                                                    setVisibility(`${aliasName}col_every_hour`, false);
                                                    setVisibility(`${aliasName}col_past_hour`, false);
                                                    setVisibility(`${aliasName}col_on`, false);
                                                    setVisibility(`${aliasName}col_from`, false);
                                                    setVisibility(`${aliasName}col_weekday`, false);
                                                    setVisibility(`${aliasName}col_onthe`, false);
                                                    setVisibility(`${aliasName}col_every_minute`, false);
                                                    setVisibility(`${aliasName}col_month`, false);
                                                })()
                                            )}

                                            {selectedschedule === "Week" && (
                                                (() => {
                                                    setVisibility(`${aliasName}col_every`, true);
                                                    setVisibility(`${aliasName}col_schedule`, true);
                                                    setVisibility(`${aliasName}col_hour`, true);
                                                    setVisibility(`${aliasName}col_at`, true);
                                                    setVisibility(`${aliasName}col_colon`, true);
                                                    setVisibility(`${aliasName}col_minute`, true);
                                                    setVisibility(`${aliasName}dvtimezone`, true);
                                                    setVisibility(`${aliasName}col_every_hour`, false);
                                                    setVisibility(`${aliasName}col_past_hour`, false);
                                                    setVisibility(`${aliasName}col_on`, true);
                                                    setVisibility(`${aliasName}col_from`, false);
                                                    setVisibility(`${aliasName}col_weekday`, true);
                                                    setVisibility(`${aliasName}col_onthe`, false);
                                                    setVisibility(`${aliasName}col_every_minute`, false);
                                                    setVisibility(`${aliasName}col_month`, false);
                                                })()
                                            )}

                                            {selectedschedule === "Month" && (
                                                (() => {
                                                    setVisibility(`${aliasName}col_every_hour`, false);
                                                    setVisibility(`${aliasName}col_past_hour`, false);
                                                    setVisibility(`${aliasName}col_on`, false);
                                                    setVisibility(`${aliasName}col_from`, false);
                                                    setVisibility(`${aliasName}col_weekday`, false);
                                                    setVisibility(`${aliasName}col_onthe`, true);
                                                    setVisibility(`${aliasName}col_every_minute`, false);
                                                    setVisibility(`${aliasName}col_every`, true);
                                                    setVisibility(`${aliasName}col_schedule`, true);
                                                    setVisibility(`${aliasName}col_hour`, true);
                                                    setVisibility(`${aliasName}col_at`, true);
                                                    setVisibility(`${aliasName}col_colon`, true);
                                                    setVisibility(`${aliasName}col_minute`, true);
                                                    setVisibility(`${aliasName}dvtimezone`, true);
                                                    setVisibility(`${aliasName}col_month`, true);
                                                })()
                                            )}

                                        </div>
                                        <div className="form-group form-control-sm mt-1" id={`${aliasName}col_on`} style={{ display: 'none' }} >
                                            <label>on</label>
                                        </div>
                                        <div className="form-group form-control-sm" id={`${aliasName}col_weekday`} style={{ display: 'none' }} >
                                            <select className="form-select form-select-sm" id={`${aliasName}dd_weekday`} onChange={handleWeekdayChange} value={selectedweekday} required>
                                                {weekdayOptions.map((option) => (
                                                    <option key={option} value={option}>
                                                        {option}
                                                    </option>
                                                ))}
                                            </select>
                                        </div>
                                        <div className="form-group form-control-sm mt-1" id={`${aliasName}col_onthe`} style={{ display: 'none' }}>
                                            <label>on the</label>
                                        </div>
                                        <div className="form-group form-control-sm" id={`${aliasName}col_month`} style={{ display: 'none' }} >
                                            <select className="form-select form-select-sm" id={`${aliasName}dd_month`} onChange={handleMonthChange} value={selectedmonth} required >
                                                {monthOptions.map((option) => (
                                                    <option key={option} value={option}>
                                                        {option}
                                                    </option>
                                                ))}
                                            </select>
                                        </div>
                                        <div className="form-group form-control-sm mt-1" id={`${aliasName}col_at`} style={{ display: 'none' }}>
                                            <label>at</label>
                                        </div>
                                        <div className="form-group form-control-sm mt-1" id={`${aliasName}col_from`} style={{ display: 'none' }}>
                                            <label>from</label>
                                        </div>
                                        <div className="form-group form-control-sm" id={`${aliasName}col_past_hour`} style={{ display: 'none' }} >
                                            <select className="form-select form-select-sm" id={`${aliasName}dd_past_hour`} onChange={handlePastHourChange} value={selectedpasthour} required>
                                                {pastHourOptions.map((option) => (
                                                    <option key={option} value={option}>
                                                        {option}
                                                    </option>
                                                ))}
                                            </select>
                                        </div>
                                        <div className="form-group form-control-sm" id={`${aliasName}col_hour`} style={{ display: 'none' }}>
                                            <select className="form-select form-select-sm" id={`${aliasName}dd_hour`} onChange={handleHourChange} value={selectedhour} required >
                                                {hourOptions.map((option) => (
                                                    <option key={option} value={option}>
                                                        {option}
                                                    </option>
                                                ))}
                                            </select>
                                        </div>
                                        <div className="form-group form-control-sm" id={`${aliasName}col_colon`} style={{ display: 'none' }}>
                                            <label>:</label>
                                        </div>
                                        <div className="form-group form-control-sm" id={`${aliasName}col_minute`} style={{ display: 'none' }}>
                                            <select className="form-select form-select-sm" id={`${aliasName}dd_minute`} onChange={handleMinuteChange} value={selectedminute} required>
                                                {minuteOptions.map((option) => (
                                                    <option key={option} value={option}>
                                                        {option}
                                                    </option>
                                                ))}
                                            </select>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-sm-1">
                                </div>
                            </div>
                            <br />

                            <div className="row">
                                <div className="col-sm-1"></div>
                                <div className="col-sm-6">
                                    <div id={`${aliasName}dvtimezone`} className="form-group form-control-sm" style={{ display: 'none' }}>
                                        <label for="lbltimezone">Time Zone <span className="bi bi-info-circle" data-bs-placement="top" data-bs-toggle="tooltip" title="Set Time Zone Using TimeZone Id like(Asia/Kolkata)"></span></label>
                                        <select className="form-select form-select-sm" id={`${aliasName}dd_timezone`} onChange={handleTimezoneChange} value={selectestimezone} required>
                                            {timezoneOptions.map((option) => (
                                                <option key={option} value={option}>
                                                    {option}
                                                </option>
                                            ))}
                                        </select>
                                    </div>
                                </div>
                                <div className="col-sm-2 mt-3 p-1">
                                    <div className="form-group form-control-sm d-grid">
                                        <button
                                            type="button"
                                            onClick={saveClick}
                                            className="btn btn-outline-primary btn-sm btn-block"
                                        >
                                            {ispsLoading ? (
                                                <ScaleLoader height={10} width={5} color={"#2E67F8"} loading={ispsLoading} />
                                            ) : (
                                                "Save"
                                            )}
                                        </button>
                                    </div>
                                </div>
                                <div className="col-sm-2 mt-3 p-1">
                                    <div className="form-group form-control-sm d-grid">
                                        <button
                                            type="button"
                                            onClick={clear}
                                            className="btn btn-outline-primary btn-sm btn-block"
                                        >
                                            Clear
                                        </button>
                                    </div>
                                </div>
                                <div className="col-sm-1"></div>
                            </div>
                            <div className="row justify-content-lg-center">
                                <div className="col-sm-1">
                                </div>
                                <div className="col-sm-10">
                                    <div id={`${aliasName}dverror`} className="form-group form-control-sm" align="center" hidden>
                                        <span id={`${aliasName}lblerror`} hidden></span>
                                    </div>
                                </div>
                                <div className="col-sm-1">
                                </div>
                            </div>
                        </div>
                    </div>
                    <br />
                    <div className="modal-footer">
                        <input type="button" value="Close" className="btn btn-outline-danger" data-dismiss="modal" data-bs-dismiss="modal" onClick={closeModal} />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ScheduleModel;