import React, { useState, useEffect } from 'react';

const NetworkStatusMessage = () => {
  const [online, setOnline] = useState(navigator.onLine);

  useEffect(() => {
    const handleOnline = () => setOnline(true);
    const handleOffline = () => setOnline(false);

    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);

    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  const contentStyle = {
    fontFamily: 'Arial, sans-serif',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '100vh',
    margin: 0
  };

  const cardStyle = {
    backgroundColor: '#f2f2f2',
    textAlign: 'center',
    padding: '50px',
    borderRadius: '10px',
    boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)'
  };

  const headingStyle = {
    color: '#333'
  };

  const paragraphStyle = {
    color: '#666'
  };

  return (
    <div style={contentStyle}>
      {!online && (
        <div style={cardStyle} className="content">
          <h1 style={headingStyle}>Oops! You're offline.</h1>
          <p style={paragraphStyle}>It seems that you're not connected to the internet right now. Please check your connection and try again later.</p>
        </div>
      )}
    </div>
  );
};

export default NetworkStatusMessage;
