import React, { useState, useEffect } from 'react';
import Header from "./Header";
import 'bootstrap/dist/js/bootstrap.bundle.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import Slidermenu from "./Slidermenu";
import axiosInstance from './axios-interceptor.js';
import TableLoader from '../Component/TableLoader'
import { PuffLoader } from "react-spinners";
import { jwtDecode } from 'jwt-decode';
import { useNavigate } from 'react-router-dom';
import secureLocalStorage from 'react-secure-storage';
import withNetworkStatus from '../Component/NetworkStatusHOC';
import AlertModel from '../Component/AlertModel';
import DataTableModel from '../Component/DataTable';
import { } from '../CSS/custom.scss';
import { useAuth } from '../Context/AuthContext';
import { Zoom, ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Datamigration_home() {
  const { usertype } = useAuth();
  const [metadatas, setMetadatas] = useState([]);
  const [tableName, setTableName] = useState('');
  const [removetableName, setRemoveTableName] = useState('');
  const [showTable, setShowTable] = useState(false);
  const [loading, setLoading] = useState(true);
  const [issrLoading, setissrLoading] = useState(false);
  const [isrmLoading, setisrmLoading] = useState(false);
  const [ProcessId, setProcessId] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [searchFocused, setSearchFocused] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalrecords, setTotalRecords] = useState(0);
  const itemsPerPage = 10;
  const totalPages = Math.ceil(totalrecords / itemsPerPage);

  const navigate = useNavigate()
  const { id } = useAuth();

  const CheckToken = async () => {
    if (!secureLocalStorage.getItem('Token')) {
      return false;
    } else {
      const decoded = jwtDecode(secureLocalStorage.getItem('Token'));
      const currentTimestamp = Math.floor(Date.now() / 1000);

      if (decoded.exp < currentTimestamp) {
        return false;
      } else {
        const display_result = await axiosInstance.get("get_token", {
          params: {
            user_id: id
          }
        });
        if (display_result.data.status) {
          const newToken = secureLocalStorage.getItem('Token');
          const storedtoken = display_result.data.data[0].token;
          if (newToken !== storedtoken) {
            return false;
          } else {
            return true;
          }
        }
      }
    }
  }

  const openModal = (process_id, source_table_name) => {
    setShowModal(true);
    setProcessId(process_id)
    setRemoveTableName(source_table_name)
  };

  const closeModal = () => {
    setShowModal(false);
  };

  useEffect(() => {
    (async () => {
      let chk_token = await CheckToken();
      if (chk_token) {
        if (tableName == "") {
          await Promise.all([Load()]);
        }
        setLoading(false);
        setShowTable(true);
      } else {
        navigate('/')
      }
    })();
  }, [currentPage]);

  async function Load() {
    let chk_token = await CheckToken();
    if (chk_token) {
      await counttotalrecords();
      const display_result = await axiosInstance.get("get_process_table", {
        params: {
          current: (currentPage - 1) * itemsPerPage,
          pagesize: itemsPerPage
        }
      });
      if (display_result.data.status) {
        setMetadatas(display_result.data.data);
      }
    } else {
      navigate('/')
    }
  }

  async function counttotalrecords() {
    const display_result = await axiosInstance.get("get_count_process_table_data", {
    });
    if (display_result.data.status) {
      setTotalRecords(display_result.data.data[0].process_id);
    }
  }

  const handlePageChange = async (newPage) => {

    let chk_token = await CheckToken();
    if (chk_token) {
      if (newPage >= 1 && newPage <= totalPages && tableName == "") {
        setCurrentPage(newPage);
      }
      else if (tableName != "" && newPage >= 1 && newPage <= totalPages) {
        setCurrentPage(newPage);
        (async () => {
          const display_result = await axiosInstance.get("get_process_table_search", {
            params: {
              Source_Table_Name: tableName,
              current: (newPage - 1) * itemsPerPage,
              pagesize: itemsPerPage
            }
          });
          try {
            if (display_result.data.status) {
              setMetadatas(display_result.data.data);
            }
          } catch (error) {
            console.error('Error while fetching data:', error);
          }
        }
        )();
      }
    } else {
      navigate('/')
    }
  };

  async function DeleteMetadata(process_id) {
    let chk_token = await CheckToken();
    if (chk_token) {
      setisrmLoading(true);
      const delete_result = await axiosInstance.delete("delete_process_table", {
        data: { process_id: process_id }
      });
      if (delete_result.data.status) {
        toast.success(delete_result.data.message, { className: 'success-toast' });
        Load();
        closeModal();
      } else {
        toast.error(delete_result.data.message, { className: 'error-toast' });
      }
      setisrmLoading(false);
    } else {
      navigate('/')
    }
  }

  async function counttotalrecords_tablename(source_table_name) {
    try {
      const display_result = await axiosInstance.get("get_count_process_table_data_tablename", {
        params: {
          sourcetablename: source_table_name
        }
      });
      if (display_result.data.status) {
        return display_result;
      }
    } catch (error) {
    }
  }


  async function handleInputChange(event) {
    setissrLoading(true);
    setTableName(event.target.value);
    const source_table_name = event.target.value;
    const table = await counttotalrecords_tablename(source_table_name);
    setTotalRecords(table.data.data[0].process_id)

    const display_result = await axiosInstance.get("get_process_table_search", {
      params: {
        Source_Table_Name: source_table_name,
        current: (currentPage - 1) * itemsPerPage,
        pagesize: itemsPerPage
      }
    });
    try {
      if (display_result.data.status) {
        setMetadatas(display_result.data.data);
      }
    } catch (error) {
      // Handle any errors here
      console.error('Error while fetching data:', error);
    }
    setissrLoading(false);
  };


  return (
    <div>
      <Header></Header>
      <Slidermenu></Slidermenu>
      <div className="mt-1 p-2 ms-5">
        <div className='container-fluid'>
          <div className='row p-2'>
            <div class="col-sm-12">
              <ToastContainer
                position="bottom-center"
                autoClose={3000}
                transition={Zoom}
              />
              <div>
                <div class="row" >
                  <h6 class="text-center display-7"><small>Data ingestion process summary</small></h6>
                </div>
                <hr />
                <div class="row">
                  <div class="col-sm-9">
                  </div>
                  <div class="col-sm-3">
                    <p align="right">
                      <div className="text-input" style={{ position: 'relative' }}>
                        <label className={searchFocused || tableName !== "" ? "label-focus" : ""} htmlFor="txtsearch">Search Staging Table</label>
                        {issrLoading && (
                          <div style={{ position: 'absolute', top: '50%', right: '25px', transform: 'translateY(-50%)' }}>
                            <PuffLoader size={21} color={"#2E67F8"} loading={issrLoading} />
                          </div>
                        )}
                        <input
                          id="txttablename"
                          type="text"
                          className={`form-control form-control-sm ${searchFocused || tableName !== "" ? "input-focus" : ""}`}
                          value={tableName}
                          onChange={handleInputChange}
                          onBlur={() => setSearchFocused(false)}
                          onFocus={() => setSearchFocused(true)}
                          required
                        />
                        <span
                          className="fa fa-search"
                          id="togglesearch"

                          style={{ position: "absolute", right: "-1rem", top: "80%", transform: "translateY(-50%)", cursor: "pointer", color: "lightgray", fontSize: "1rem" }}
                        />
                      </div>
                    </p>
                  </div>
                </div>
                {(usertype === process.env.REACT_APP_ADMIN) && (
                  <div>
                    {loading ? (
                      // Display the spinner when loading is true
                      <TableLoader loading={loading} />
                    ) : (
                      <DataTableModel
                        showTable={showTable}
                        partitionHeaders={true}
                        headings={metadatas.length > 0 ? [
                          ...Object.keys(metadatas[0]).slice(2).map((key, index) => (
                            <th key={index}>{key}</th>
                          )),
                          <th key="Actions">Actions</th>
                        ] : []}
                        items={metadatas}
                        removeItems={["process_id", "STable Name"]}
                        removeActions={openModal}
                        currentPage={currentPage}
                        totalPages={totalPages}
                        prevPage={() => handlePageChange(currentPage - 1)}
                        currPage={handlePageChange}
                        nxtPage={() => handlePageChange(currentPage + 1)}
                      />
                    )}
                  </div>
                )}
                {(usertype === process.env.REACT_APP_ANALYST || usertype === process.env.REACT_APP_EDITOR) && (
                  <div>
                    {loading ? (
                      // Display the spinner when loading is true
                      <TableLoader loading={loading} />
                    ) : (
                      <DataTableModel
                        showTable={showTable}
                        partitionHeaders={true}
                        headings={[
                          ...Object.keys(metadatas[0]).slice(2).map((key, index) => (
                            <th key={index}>{key}</th>
                          )),
                        ]}
                        items={metadatas}
                        currentPage={currentPage}
                        totalPages={totalPages}
                        prevPage={() => handlePageChange(currentPage - 1)}
                        currPage={handlePageChange}
                        nxtPage={() => handlePageChange(currentPage + 1)}
                      />
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
          <AlertModel
            modelTitle={"Remove Process Status"}
            lblName={<>remove <b>{removetableName}</b> table?</>}
            showModal={showModal}
            closeModal={closeModal}
            isalLoading={isrmLoading}
            saveClick={() => DeleteMetadata(ProcessId)}
            btnname={"Remove"}
            btncolor={"#DC3545"}
            btnclass={"btn btn-outline-danger btn-sm btn-block"}
          />
        </div>
      </div>
    </div>
  );
}

export default withNetworkStatus(Datamigration_home);