import React, { useState, useEffect, useRef } from "react";
import Header from "./Header";
import axiosInstance from './axios-interceptor.js';
import CryptoJS from 'crypto-js';
import { ScaleLoader, PuffLoader } from "react-spinners";
import { jwtDecode } from 'jwt-decode';
import { useNavigate } from 'react-router-dom';
import secureLocalStorage from 'react-secure-storage';
import withNetworkStatus from '../Component/NetworkStatusHOC';
import { } from '../CSS/custom.scss';
import { useAuth } from '../Context/AuthContext';
import { Zoom, ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import AlertModel from "../Component/AlertModel";

function Codetranslation() {
  const { id, usertype, username } = useAuth();
  const navigate = useNavigate()
  const textRef = useRef(null);
  const [formValid, setFormValid] = useState(false);
  const [formPathValid, setFormPathValid] = useState(false);
  const [editMode, setEditMode] = useState(false);
  const [failVisible, setFailVisible] = useState(false);
  const [scVisible, setSCVisible] = useState(false);
  const [isbtnrunExists, setIsBtnRunExists] = useState(true);
  const [isbtneditExists, setIsBtnEditExists] = useState(true);
  const [isddExists, setIsDdExists] = useState(true);
  const [islblExists, setIsLblExists] = useState(true);
  const [resultStatus, setResultStatus] = useState('');
  const [sourcedatabase, setSourceDatabase] = useState([]);
  const [sourceschema, setSourceSchema] = useState([]);
  const [storedprocedure, setStoredProcedure] = useState([]);
  const [viewStoredProcedure, setviewStoredProcedure] = useState('');

  const [selectedDatabase, setSelectedDatabase] = useState('');
  const [fileName, setfileName] = useState('');
  const [selectedSchema, setSelectedSchema] = useState('');
  const [selectedProcedure, setSelectedProcedure] = useState('');
  const [sourcedatabaseFocused, setSourceDatabaseFocused] = useState(false);
  const [sourceschemaFocused, setSourceSchemaFocused] = useState(false);
  const [storeprocedureFocused, setStoreProcedureFocused] = useState(false);
  const [maxoccurrenceFocused, setMaxOccurrenceFocused] = useState(false);

  const [issdbLoading, setissdbLoading] = useState(false);
  const [isssLoading, setisssLoading] = useState(false);
  const [isctLoading, setisctLoading] = useState(false);
  const [isvcLoading, setisvcLoading] = useState(false);
  const [isrcLoading, setisrcLoading] = useState(false);
  const [isscLoading, setisscLoading] = useState(false);
  const [targetschemaFocused, setTargetSchemaFocused] = useState(false);
  const [promptFocused, setPromptFocused] = useState(false);
  const [procedureFocused, setProcedureFocused] = useState(false);
  const [pathFocused, setPathFocused] = useState(false);
  const [targetSchema, setTargetSchema] = useState('');
  const [customPrompt, setCustomPrompt] = useState('');
  const [path, setPath] = useState('');
  const [selectedMaxOccurrence, setSelectedMaxOccurrence] = useState('3');
  const [showbutton, setShowButton] = useState(false);
  const [showsp, setShowSP] = useState(false);

  const [showclear, setShowClear] = useState(false);
  const [showModal_Popstcluster, setShowModal_Popstcluster] = useState(false);
  const [isscsLoading, setSCSLoading] = useState(false);
  const [issdLoading, setSdLoading] = useState(false);
  const [clusterId, setClusterId] = useState('');
  const [clusterName, setClusterName] = useState(null);
  const [pathbuttonid, setPathButtonId] = useState('');

  useEffect(() => {
    if (selectedDatabase.length !== 0 || selectedSchema.length !== 0 || selectedProcedure.length !== 0 || targetSchema !== '' || customPrompt !== '') {
      setShowClear(true);
    } else {
      setShowClear(false);
    }
  }, [selectedDatabase, selectedSchema, selectedProcedure, targetSchema, customPrompt]);

  const CheckToken = async () => {
    if (!secureLocalStorage.getItem('Token')) {
      return false;
    } else {
      const decoded = jwtDecode(secureLocalStorage.getItem('Token'));
      const currentTimestamp = Math.floor(Date.now() / 1000);

      if (decoded.exp < currentTimestamp) {
        return false;
      } else {
        const display_result = await axiosInstance.get("get_token", {
          params: {
            user_id: id
          }
        });
        if (display_result.data.status) {
          const newToken = secureLocalStorage.getItem('Token');
          const storedtoken = display_result.data.data[0].token;
          if (newToken !== storedtoken) {
            return false;
          } else {
            return true;
          }
        }
      }
    }
  }

  const handlePathSubmit = async (e) => {
    e.preventDefault();
    setisvcLoading(true);
    let chk_token = await CheckToken();
    if (chk_token) {
      get_db_cluster_id("btnpath");
    } else {
      navigate('/')
      setisvcLoading(false);
    }
  };

  useEffect(() => {
    if (path) {
      setFormPathValid(true);
    } else {
      setFormPathValid(false);
    }
  }, [path]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setisctLoading(true);
    let chk_token = await CheckToken();
    if (chk_token) {
      get_db_cluster_id(null);
    } else {
      navigate('/')
      setisctLoading(false);
    }
  };

  const openModal_Popstcluster = () => {
    setShowModal_Popstcluster(true);
  };

  const closeModal_Popstcluster = () => {
    setShowModal_Popstcluster(false);
    setisvcLoading(false);
    setisctLoading(false);
  };

  const handleClusterStartClick = async () => {
    let chk_token = await CheckToken();
    if (chk_token) {
      //event.preventDefault();
      setSCSLoading(true);
      const response = await axiosInstance.post("post_start_cluster", {
        cluster_id: clusterId,
      });
      if (response) {
        if (pathbuttonid === "btnpath") {
          await get_cluster_status(clusterId, "btnpath");
        }
        else {
          await get_cluster_status(clusterId, null);
        }

      }
    } else {
      navigate('/')
    }
  }

  async function Decrypt(encryptedPassword) {
    const key = CryptoJS.enc.Utf8.parse('GHYZXFZH2A4B6N6P7R9SPNMT');
    const decrypted = CryptoJS.TripleDES.decrypt(
      {
        ciphertext: CryptoJS.enc.Base64.parse(encryptedPassword),
      },
      key,
      {
        mode: CryptoJS.mode.ECB,
        padding: CryptoJS.pad.Pkcs7,
      }
    );
    return decrypted.toString(CryptoJS.enc.Utf8);
  }

  const get_db_cluster_id = async (db_name) => {
    setPathButtonId('')
    let chk_token = await CheckToken();
    if (chk_token) {
      setSdLoading(true);
      const display_result = await axiosInstance.get("get_codetranslation_cluster_id", {
      });
      if (display_result.data) {
        const result = display_result.data;
        const cluster_id = result.settings.existing_cluster_id;
        setClusterId(cluster_id);
        setPathButtonId(db_name)
        await get_cluster_status(cluster_id, db_name);
      }
    } else {
      navigate('/')
    }
  };

  const get_cluster_status = async (cluster_id, db_name) => {
    let chk_token = await CheckToken();
    if (chk_token) {
      const display_results = await axiosInstance.get("get_cluster_detail", {
        params: {
          cluster_id: cluster_id,
        }
      });
      if (display_results.data) {
        const json_value = display_results.data;
        setClusterName(json_value.cluster_name);
        if (json_value.state === "PENDING") {
          setTimeout(() => {
            get_cluster_status(cluster_id, db_name);
          }, 2000);
        }
        else if (json_value.state === "RUNNING") {
          closeModal_Popstcluster();
          setSCSLoading(false);
          setSdLoading(false);
          if (db_name === "btnpath") {
            if (formPathValid) {
              await handleViewClick();
            }
          }
          else {
            if (formValid) {
              await handleSubmitClick();
            }
          }
        }
        else if (json_value.state === "TERMINATED") {
          openModal_Popstcluster();
          setSdLoading(false);
        }
      } else {
        //console.error("Data is not an array:", display_results.data);
      }
    } else {
      navigate('/')
    }
  }

  useEffect(() => {
    if (selectedDatabase && selectedSchema && selectedProcedure && targetSchema) {
      setFormValid(true);
    } else {
      setFormValid(false);
    }
  }, [selectedDatabase, selectedSchema, selectedProcedure, targetSchema]);

  useEffect(() => {
    (async () => {
      let chk_token = await CheckToken();
      if (chk_token) {
        await Promise.all([SourceDatabase()]);
      } else {
        navigate('/')
      }
    })();
  }, []);

  async function SourceDatabase() {
    const display_result = await axiosInstance.get("get_database_name_migration", {
    });
    if (display_result.data.status) {
      setSourceDatabase(display_result.data.data);
    }
  }

  async function SourceSchema(database_name) {
    setSelectedDatabase('');
    setSourceSchema([]);
    setSelectedSchema('');
    let chk_token = await CheckToken();
    if (chk_token) {
      setSelectedDatabase(database_name)
      setissdbLoading(true);
      const res = await ConnectionString(database_name);
      const username = await Decrypt(res.data.data[0].username);
      const password = await Decrypt(res.data.data[0].password);
      const hostname = await Decrypt(res.data.data[0].hostname);

      const display_result = await axiosInstance.get("get_mssql_schema_name", {
        params: {
          user: username,
          password: password,
          host: hostname
        }
      });
      if (display_result.data.status) {
        setSourceSchema(display_result.data.data.recordsets[0]);
      }
      setissdbLoading(false);
    } else {
      navigate('/')
    }
  }

  async function StoredProcedure(schema_name) {
    setSelectedProcedure('');
    setStoredProcedure([]);
    let chk_token = await CheckToken();
    if (chk_token) {
      setisssLoading(true);
      setSelectedSchema(schema_name);
      const res = await ConnectionString(selectedDatabase);
      const username = await Decrypt(res.data.data[0].username);
      const password = await Decrypt(res.data.data[0].password);
      const hostname = await Decrypt(res.data.data[0].hostname);

      const display_result = await axiosInstance.get("get_mssql_storedprocedure_name", {
        params: {
          schema_name: schema_name,
          user: username,
          password: password,
          host: hostname
        }
      });
      if (display_result.data.status) {
        setStoredProcedure(display_result.data.data.recordsets[0]);
      }
      setisssLoading(false);
    } else {
      navigate('/')
    }
  }

  async function ConnectionString(database_name) {
    const display_result = await axiosInstance.get("get_connection_string", {
      params: {
        user_name: username,
        database_name: database_name
      }
    });
    return display_result
  }

  async function Decrypt(encryptedPassword) {
    const key = CryptoJS.enc.Utf8.parse('GHYZXFZH2A4B6N6P7R9SPNMT');
    const decrypted = CryptoJS.TripleDES.decrypt(
      {
        ciphertext: CryptoJS.enc.Base64.parse(encryptedPassword),
      },
      key,
      {
        mode: CryptoJS.mode.ECB,
        padding: CryptoJS.pad.Pkcs7,
      }
    );
    return decrypted.toString(CryptoJS.enc.Utf8);
  }

  const handleStoredProcedureChange = (event) => {
    setSelectedProcedure(event.target.value);
  };

  const handleTargetSchemaChange = (event) => {
    const newValue = event.target.value;
    setTargetSchema(newValue);
  };

  const handleCustomPromptChange = (event) => {
    const newValue = event.target.value;
    setCustomPrompt(newValue);
  };

  const handleResetClick = async (event) => {
    event.preventDefault();
    setSelectedDatabase('');
    setSourceSchema([]);
    setSelectedSchema('');

    setStoredProcedure([]);
    setSelectedProcedure('');
    setTargetSchema('');
    setCustomPrompt('');
    setPath('');
    setSelectedMaxOccurrence('3');
    setfileName('');
    setviewStoredProcedure('');
    setIsBtnEditExists(true);
    setIsBtnRunExists(true);
    setIsLblExists(true);
    setIsDdExists(true);
    setEditMode(false);
    setSCVisible(false);


    setFailVisible(false);
    setisrcLoading(false);

    setShowButton(false);
    setShowSP(false);
  };

  async function handleSubmitClick() {
    let chk_token = await CheckToken();
    if (chk_token) {
      setisctLoading(true);
      const catalog = process.env.REACT_APP_development;
      let databricks_job_runid = "";
      const response = await axiosInstance.post("code_translation_job", {
        catalog: catalog,
        storedprocedure_name: selectedProcedure,
        source_schema: selectedSchema.toLowerCase(),
        target_schema: targetSchema,
        custom_prompt: customPrompt
      });
      if (response) {
        databricks_job_runid = response.data.run_id;
        getdata(databricks_job_runid);
      }
    } else {
      navigate('/')
    }
  };

  async function getdata(db_runid) {
    let chk_token = await CheckToken();
    if (chk_token) {
      const display_result = await axiosInstance.get("get_job_status", {
        params: { db_runid: db_runid },
      });
      if (display_result.data) {
        let result = display_result.data;

        if (result.metadata.state.life_cycle_state === 'TERMINATED') {
          setResultStatus(result.metadata.state.result_state);

          if (result.metadata.state.result_state === 'SUCCESS') {
            let output = result.notebook_output.result;
            setPath(output);
            setisctLoading(false);
          } else {
            setisctLoading(false);
          }
        } else if (result.metadata.state.life_cycle_state === 'PENDING' || result.metadata.state.life_cycle_state === 'RUNNING') {
          setTimeout(() => {
            getdata(db_runid);
          }, 2000);
        }
      }
    } else {
      navigate('/')
    }
  }

  const handlePathChange = (event) => {
    event.preventDefault();
    const newValue = event.target.value;
    setPath(newValue);
  };

  const copyToClipboard = (event) => {
    event.preventDefault();
    try {
      if (textRef.current) {
        navigator.clipboard.writeText(textRef.current.value);
      }
    } catch (err) {
      // if (textRef.current) {
      //   textRef.current.select();
      //   document.execCommand('copy');
      // }
    }
  };

  const handleViewClick = async () => {
    try {
      setisvcLoading(true);
      if (textRef.current) {
        const file_path = textRef.current.value;
        RunViewSP(file_path);
      }
    } catch (err) {
    }
  };

  async function RunViewSP(file_path) {
    let chk_token = await CheckToken();
    if (chk_token) {
      let databricks_job_runid = "";
      const response = await axiosInstance.post("read_code_translation_job", {
        file_path: file_path
      });
      if (response) {
        databricks_job_runid = response.data.run_id;
        getspdata(databricks_job_runid);
      }
    } else {
      navigate('/')
    }
  }

  async function getspdata(db_runid) {
    let chk_token = await CheckToken();
    if (chk_token) {
      const display_result = await axiosInstance.get("get_job_status", {
        params: { db_runid: db_runid },
      });
      if (display_result.data) {
        let result = display_result.data;
        if (result.metadata.state.life_cycle_state === 'TERMINATED') {
          setResultStatus(result.metadata.state.result_state);
          let file_path = result.metadata.overriding_parameters.notebook_params.file_path;
          if (result.metadata.state.result_state === 'SUCCESS') {
            setShowButton(true);
            setShowSP(true);
            let output_data = result.notebook_output.result;
            setviewStoredProcedure(output_data);
            const search_string = '/';
            const lastIndex = file_path.lastIndexOf(search_string);
            if (lastIndex >= 0) {
              const editname = file_path.substring(lastIndex + 1);
              const fileformat = editname.substring(editname.lastIndexOf('.') + 1).trim();

              if (fileformat === 'sql') {
                setfileName(editname);
                setIsBtnEditExists(true);
                setIsBtnRunExists(true);
                setIsLblExists(true);
                setIsDdExists(true);
              } else {
                setfileName(editname);
                if (output_data === "File Not Found") {
                  setIsBtnEditExists(true);
                  setIsBtnRunExists(true);
                  setIsLblExists(true);
                  setIsDdExists(true);
                  setShowButton(false);
                } else {
                  setIsBtnEditExists(false);
                  setIsBtnRunExists(false);
                  setIsLblExists(false);
                  setIsDdExists(false);
                  setShowButton(true);
                }
              }
              setisvcLoading(false);
            }
          } else {
            setisvcLoading(false);
            setShowButton(false);
            setShowSP(false);
          }
        } else if (result.metadata.state.life_cycle_state === 'PENDING' || result.metadata.state.life_cycle_state === 'RUNNING') {
          setTimeout(() => {
            getspdata(db_runid);
          }, 2000);
        }
      }
    } else {
      navigate('/')
    }
  }

  const handleEditSpEditor = (e) => {
    let newValue = e.target.value;
    setviewStoredProcedure(newValue);
  }

  const handleEditClick = (event) => {
    event.preventDefault();
    setEditMode(true);
    setSCVisible(true);
  };

  const handleMaxOccurrenceChange = (event) => {
    const newValue = event.target.value;
    setSelectedMaxOccurrence(newValue);
  };

  const maxOccurrenceOptions = Array.from({ length: 10 }, (_, index) => (
    <option key={index + 1} value={index + 1}>
      {index + 1}
    </option>
  ));

  async function handleRunCodeClick(event) {
    event.preventDefault();
    try {
      let chk_token = await CheckToken();
      if (chk_token) {
        setisrcLoading(true);
        let databricks_job_runid = "";
        if (textRef.current) {
          const file_path = textRef.current.value;
          const max_occurrence = selectedMaxOccurrence;
          const response = await axiosInstance.post("run_code_translation_job", {
            file_path: file_path,
            max_occurrence: max_occurrence
          });
          if (response) {
            databricks_job_runid = response.data.run_id;
            putrundata(databricks_job_runid);
          }
        } else {
          navigate('/')
        }
      }
    } catch (err) {
    }
  };

  async function putrundata(db_runid) {
    let chk_token = await CheckToken();
    if (chk_token) {
      const display_result = await axiosInstance.get("get_job_status", {
        params: { db_runid: db_runid },
      });
      if (display_result.data) {
        let result = display_result.data;

        if (result.metadata.state.life_cycle_state === 'TERMINATED') {
          setResultStatus(result.metadata.state.result_state);
          if (result.metadata.state.result_state === 'SUCCESS') {
            let output_run_msg = result.notebook_output.result;
            if (output_run_msg === 'Success') {
              toast.success("Success!", { className: 'success-toast' });
              setFailVisible(false);
              setisrcLoading(false);
            } else {
              toast.error("Failed!", { className: 'error-toast' });
              setFailVisible(true);
              setisrcLoading(false);
            }
            setEditMode(false);
          } else {
            toast.error("Failed!", { className: 'error-toast' });
            setFailVisible(true);
            setisrcLoading(false);
          }
        } else if (result.metadata.state.life_cycle_state === 'PENDING' || result.metadata.state.life_cycle_state === 'RUNNING') {
          setTimeout(() => {
            putrundata(db_runid);
          }, 2000);
        }
      }
    } else {
      navigate('/')
    }
  }

  async function handleSaveClick() {
    try {
      let chk_token = await CheckToken();
      if (chk_token) {
        setisscLoading(true);
        let databricks_job_runid = "";
        if (textRef.current) {
          const file_path = textRef.current.value;
          const response = await axiosInstance.post("update_code_translation_job", {
            file_path: file_path,
            code: viewStoredProcedure
          });
          if (response) {
            databricks_job_runid = response.data.run_id;
            putupdatedata(databricks_job_runid);
          }
        } else {
          navigate('/')
        }
      }
    } catch (err) {
    }
  };

  async function putupdatedata(db_runid) {
    let chk_token = await CheckToken();
    if (chk_token) {
      const display_result = await axiosInstance.get("get_job_status", {
        params: { db_runid: db_runid },
      });
      if (display_result.data) {
        let result = display_result.data;

        if (result.metadata.state.life_cycle_state === 'TERMINATED') {
          setResultStatus(result.metadata.state.result_state);
          if (result.metadata.state.result_state === 'SUCCESS') {
            let output_msg = result.notebook_output.result;
            setEditMode(false);
            toast.success(output_msg, { className: 'success-toast' });
            setisscLoading(false);
            setSCVisible(false);
          } else {
            setisscLoading(false);
          }
        } else if (result.metadata.state.life_cycle_state === 'PENDING' || result.metadata.state.life_cycle_state === 'RUNNING') {
          setTimeout(() => {
            putupdatedata(db_runid);
          }, 2000);
        }
      }
    } else {
      navigate('/')
    }
  }

  const handleCloseClick = () => {
    setEditMode(false);
    setSCVisible(false);
  };

  return (
    <div>
      <Header></Header>
      <div className="mt-5 p-4">
        <ToastContainer
          position="bottom-center"
          autoClose={3000}
          transition={Zoom}
        />
        <div class="row">
          <div class="col-sm-3">
            <div class="tab-content">
              <div id="sp" class="container tab-pane active">
                <form onSubmit={handleSubmit}>
                  <div class="container-sm border">
                    <h6 className="text-center display-7"><small>Perfom Code Translation</small></h6>
                    <hr></hr>
                    <div className="row">
                      <div class="col-sm-12">
                        <div class="form-group form-control-sm">
                          <div className="text-input" style={{ position: 'relative' }}>
                            <label className={sourcedatabaseFocused || selectedDatabase !== "" ? "label-focus" : ""} htmlFor="ddsourcedatabase">
                              Source Database
                            </label>
                            <div className="select-wrapper">
                              <select
                                className={`form-control form-control-sm select-input ${sourcedatabaseFocused || selectedDatabase !== "" ? "input-focus" : ""} `}
                                id="ddsourcedatabase"
                                name="ddsourcedatabase"
                                onChange={(event) => SourceSchema(event.target.value)}
                                onBlur={() => setSourceDatabaseFocused(false)}
                                onFocus={() => setSourceDatabaseFocused(true)}
                                required
                                autoComplete="off"
                                value={selectedDatabase}
                              >
                                <option value="" hidden></option>
                                {sourcedatabase.map((sourceDatabase) => (
                                  <option key={sourceDatabase.database_type} value={sourceDatabase.database_name}>
                                    {sourceDatabase.database_name}
                                  </option>
                                ))}
                              </select>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>

                    <div class="row">
                      <div class="col-sm-12">
                        <div class="form-group form-control-sm">
                          <div className="text-input" style={{ position: 'relative' }}>
                            <label className={sourceschemaFocused || selectedSchema !== "" ? "label-focus" : ""} htmlFor="ddsourcedatabase">
                              Source Schema
                            </label>
                            {issdbLoading && (
                              <div style={{ position: 'absolute', top: '50%', right: '8px', transform: 'translateY(-50%)' }}>
                                <PuffLoader size={21} color={"#2E67F8"} loading={issdbLoading} />
                              </div>
                            )}
                            <div className="select-wrapper">
                              <select
                                className={`form-control form-control-sm select-input ${sourceschemaFocused || selectedSchema !== "" ? "input-focus" : ""} `}
                                id="ddsourceschema"
                                name="ddsourceschema"
                                value={selectedSchema}
                                onChange={(event) => StoredProcedure(event.target.value)}
                                onBlur={() => setSourceSchemaFocused(false)}
                                onFocus={() => setSourceSchemaFocused(true)}
                                required
                                autoComplete="off"
                              >
                                <option value="" hidden></option>
                                {sourceschema.map((sourceSchema) => (
                                  <option key={sourceSchema.database_id} value={sourceSchema.databaseName}>
                                    {sourceSchema.databaseName}
                                  </option>
                                ))}
                              </select>
                            </div>
                          </div>
                        </div>
                      </div>

                    </div>
                    <div class="row">
                      <div class="col-sm-12">
                        <div class="form-group form-control-sm">
                          <div className="text-input">
                            <label className={storeprocedureFocused || selectedProcedure !== "" ? "label-focus" : ""} htmlFor="ddsourcedatabase">Stored Procedure</label>
                            {isssLoading && (
                              <div style={{ position: 'absolute', top: '50%', right: '8px', transform: 'translateY(-50%)' }}>
                                <PuffLoader size={21} color={"#2E67F8"} loading={isssLoading} />
                              </div>
                            )}
                            <div className="select-wrapper">
                              <select
                                className={`form-control form-control-sm select-input ${storeprocedureFocused || selectedProcedure !== "" ? "input-focus" : ""} `}
                                id="ddstoredProcedure"
                                value={selectedProcedure}
                                style={{ marginBottom: '0.5rem' }}
                                onChange={handleStoredProcedureChange}
                                onBlur={() => setStoreProcedureFocused(false)} onFocus={() => setStoreProcedureFocused(true)}
                                required
                              >
                                {(
                                  <option value="" hidden></option>
                                )}
                                {storedprocedure.map((storedProcedure) => (
                                  <option key={storedProcedure.ROUTINE_NAME} value={storedProcedure.ROUTINE_NAME}>
                                    {storedProcedure.ROUTINE_NAME}
                                  </option>
                                ))}
                              </select>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-sm-12">
                        <div class="form-group form-control-sm">
                          <div className="text-input">
                            <label className={targetschemaFocused || targetSchema !== "" ? "label-focus" : ""} for="ddtargetschema">Target Schema</label>
                            <input
                              type="text"
                              id="txttargetschema"
                              className={`form-control form-control-sm ${targetschemaFocused || targetSchema !== "" ? "input-focus" : ""}`}
                              value={targetSchema}
                              onChange={handleTargetSchemaChange}
                              onBlur={() => setTargetSchemaFocused(false)}
                              onFocus={() => setTargetSchemaFocused(true)}
                              required
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-sm-12">
                        <div class="form-group form-control-sm">
                          <div className="text-input">
                            <label className={promptFocused || customPrompt !== "" ? "label-focus" : ""} for="ddcustomprompt">Custom Prompt</label>
                            <textarea
                              id="txtcustomprompt"
                              className={`form-control form-control-sm ${promptFocused || customPrompt !== "" ? "input-focus" : ""}`}
                              value={customPrompt}
                              onBlur={() => setPromptFocused(false)}
                              onFocus={() => setPromptFocused(true)}
                              onChange={handleCustomPromptChange}
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                    <div class="row mb-1">
                      <div class="col-sm-12 m-auto">
                        <div class="form-group form-control-sm">
                          <div class="d-flex justify-content-end">
                            {showclear && (
                              <button
                                className="btn btn-outline-primary btn-sm btn-block me-2"
                                onClick={handleResetClick}
                              >
                                Clear
                              </button>
                            )}
                            <button
                              className="btn btn-outline-primary btn-sm btn-block"
                              type="submit"
                            >
                              {isctLoading ? (
                                // Display the spinner when loading is true
                                <ScaleLoader height={10} width={5} color={"#2E67F8"} loading={isctLoading} />
                              ) : (
                                "Submit"
                              )}
                            </button>
                          </div>
                        </div>
                      </div>

                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
          <div class="col-sm-9">
            <div class="container-sm border">
              <div class="row">
                <h6 class="text-center display-7"><small>Code Translation</small></h6>
              </div>
              <hr></hr>
              <div class="p-0"></div>
              <div id="runcodetranslation_path">
                <form onSubmit={handlePathSubmit}>
                  <div class="container-fluid-sm">
                    <div class="row mt-1">
                      <div class="input-group input-control-sm">
                        <div class="form-group form-control-sm mt-2">
                        </div>
                        <div class="form-group form-control-sm w-50">
                          <div className="text-input">
                            <label className={pathFocused || path !== "" ? "label-focus" : ""} for="txtpath">Path</label>
                            <input
                              type="text"
                              id="txtpath"
                              className={`form-control form-control-sm ${pathFocused || path !== "" ? "input-focus" : ""}`}
                              required
                              value={path}
                              ref={textRef}
                              onBlur={() => setPathFocused(false)}
                              onFocus={() => setPathFocused(true)}
                              onChange={handlePathChange}
                            />

                          </div>

                        </div>
                        <div class="form-group form-control-sm me-auto">
                          <button
                            className="bi bi-files btn btn-sm"
                            id="btncopy"
                            title="Copy Path"
                            onClick={copyToClipboard}
                          />
                          <button
                            className="btn btn-outline-primary btn-sm btn-block mt-1"
                            id="btnview"
                            type="sumbit"
                          >
                            {isvcLoading ? (
                              // Display the spinner when loading is true
                              <ScaleLoader height={10} width={5} color={"#2E67F8"} loading={isvcLoading} />
                            ) : (
                              "Show"
                            )}
                          </button>
                        </div>
                        {showbutton && (
                          <>
                            {(usertype === process.env.REACT_APP_ADMIN || usertype === process.env.REACT_APP_EDITOR) && (
                              <div class="form-group form-control-sm">
                                <button
                                  className="btn btn-outline-primary btn-sm btn-block mt-1"
                                  id="btnedit"
                                  onClick={handleEditClick}
                                  disabled={isbtneditExists}
                                >
                                  Edit
                                </button>
                              </div>
                            )}
                            {(usertype === process.env.REACT_APP_ADMIN || usertype === process.env.REACT_APP_EDITOR) && (
                              <div class="form-group form-control-sm">
                                <div className="text-input" style={{ position: 'relative' }}>
                                  <label className={maxoccurrenceFocused || selectedMaxOccurrence !== "" ? "label-focus" : ""} disabled={islblExists} htmlFor="comment">
                                    Max Occurrence
                                  </label>
                                  <div className="select-wrapper">
                                    <select
                                      className={`form-control form-control-sm select-input bg-white ${maxoccurrenceFocused || selectedMaxOccurrence !== "" ? "input-focus" : ""} `}
                                      style={{ width: '120px' }}
                                      id="ddlmax_occurrence"
                                      value={selectedMaxOccurrence}
                                      onChange={handleMaxOccurrenceChange}
                                      disabled={isddExists}
                                      required
                                      onBlur={() => setMaxOccurrenceFocused(false)}
                                      onFocus={() => setMaxOccurrenceFocused(true)}
                                    >
                                      <option disabled hidden></option>
                                      {maxOccurrenceOptions}
                                    </select>
                                  </div>
                                </div>
                              </div>
                            )}
                            {(usertype === process.env.REACT_APP_ADMIN || usertype === process.env.REACT_APP_EDITOR) && (
                              <div class="form-group form-control-sm">
                                <button
                                  className="btn btn-outline-primary btn-sm btn-block mt-1"
                                  id="btnrun"
                                  disabled={isbtnrunExists}
                                  onClick={handleRunCodeClick}
                                >
                                  {isrcLoading ? (
                                    // Display the spinner when loading is true
                                    <ScaleLoader height={10} width={5} color={"#2E67F8"} loading={isrcLoading} />
                                  ) : (
                                    "Run Code"
                                  )}
                                </button>
                              </div>
                            )}
                            {(usertype === process.env.REACT_APP_ADMIN || usertype === process.env.REACT_APP_EDITOR) && (
                              <div class="form-group form-control-sm mt-1">
                                <label id="emptytextpath" hidden class="text-danger"></label>
                              </div>
                            )}
                          </>
                        )}
                      </div>
                    </div>
                  </div>
                </form>
              </div>
              {showsp && (
                <div class="row mt-1">
                  <div class="col-sm-12">
                    <div id="div_runcodetranslation">
                      <div class="container-fluid-sm">
                        <div class="row">
                          <div class="col-sm-12">
                            <div class="p-1"></div>
                            <div class="form-group form-control-sm">
                              <div className="text-input">
                                <label className={procedureFocused || viewStoredProcedure !== "" ? "label-focus" : ""} for="txtsp">{fileName === null ? 'stored procedure' : fileName}</label>

                                <textarea
                                  id="txtspeditor"
                                  className={`form-control form-control-sm ${procedureFocused || viewStoredProcedure !== "" ? "input-focus" : ""}`}
                                  rows="15"
                                  value={viewStoredProcedure}
                                  onChange={handleEditSpEditor}
                                  onBlur={() => setProcedureFocused(false)}
                                  onFocus={() => setProcedureFocused(true)}
                                  readOnly={!editMode}
                                />
                              </div>
                            </div>
                            {(usertype === process.env.REACT_APP_ADMIN || usertype === process.env.REACT_APP_EDITOR) && (
                              <div class="form-group form-control-sm text-end">
                                <button
                                  className="btn btn-outline-primary btn-sm btn-block me-2"
                                  id="btnsave"
                                  onClick={handleSaveClick}
                                  hidden={!scVisible}
                                >
                                  {isscLoading ? (
                                    // Display the spinner when loading is true
                                    <ScaleLoader height={10} width={5} color={"#2E67F8"} loading={isscLoading} />
                                  ) : (
                                    "Save"
                                  )}
                                </button>
                                <button
                                  className="btn btn-outline-primary btn-sm btn-block"
                                  id="btnclose"
                                  onClick={handleCloseClick}
                                  hidden={!scVisible}
                                >
                                  Close
                                </button>
                              </div>
                            )}
                          </div>

                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              )}
              <br />
            </div>
          </div>
        </div>
        <AlertModel
          modelTitle={"Cluster"}
          lblName={<>start <b>{clusterName}</b> Cluster ?</>}
          showModal={showModal_Popstcluster}
          closeModal={closeModal_Popstcluster}
          isalLoading={isscsLoading}
          saveClick={handleClusterStartClick}
          btnname={"Start"}
          btncolor={"#2E67F8"}
          btnclass={"btn btn-outline-primary btn-sm btn-block"}
        />
      </div >
    </div >
  );
}

export default withNetworkStatus(Codetranslation);