import React from "react";
import Exocube from '../Images/logo/Exocube.png'
import Usericon from '../Images/logo/person-fill.png'
import { Link, useLocation,useNavigate } from "react-router-dom";
import '../CSS/style.css'
import { useAuth } from '../Context/AuthContext';
import secureLocalStorage from 'react-secure-storage';


const Header = () => {
    let location = useLocation();
    const {firstname, logout } = useAuth();
    const navigate = useNavigate();

    const handleLogout = () => {
        secureLocalStorage.removeItem('Token');
        secureLocalStorage.removeItem('username');
        secureLocalStorage.removeItem('password');
        secureLocalStorage.removeItem('rememberMe');
        logout();
        navigate('/');
      };

    return (<>
        <div>
            <nav className="navbar navbar-expand-sm bg-dark navbar-dark fixed-top">
                <div className="container-fluid">
                    <a className={location.pathname == "/Home" || location.pathname == "/" ? "nav-item" : "nav-item"}>
                        <Link to="/Home" className="nav-link"><img className="navbar-brand" src={Exocube} alt="Exocube" style={{ width: "100px" }} /></Link>
                    </a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#collapsibleNavbar">
                        <span className="navbar-toggler-icon">
                        </span>
                    </button>
                    <div className="collapse navbar-collapse" id="collapsibleNavbar">
                        <ul className="navbar-nav">
                            <li className={location.pathname == "/Sources" ? "nav-item" : "nav-item"}>
                                <Link to="/Sources" className={location.pathname == "/Sources" ? "nav-link active" : "nav-link"}>Sources</Link>
                            </li>
                            <li className={(location.pathname == "/Datamigration_home" || location.pathname === "/Datamigration_metadata"  || location.pathname === "/Datamigration_migratetable"  || location.pathname === "/Datamigration_job_status") ? "nav-item " : "nav-item"}>
                                <Link to="/Datamigration_home" className={(location.pathname == "/Datamigration_home" || location.pathname === "/Datamigration_metadata"  || location.pathname === "/Datamigration_migratetable"  || location.pathname === "/Datamigration_job_status") ? "nav-link active" : "nav-link"}>Data Migration</Link>
                            </li>
                            <li className={location.pathname == "/Dataquality" ? "nav-item " : "nav-item"}>
                                <Link to="/Dataquality" className={location.pathname == "/Dataquality" ? "nav-link active" : "nav-link"}>Data Quality</Link>
                            </li>

                            <li className={location.pathname == "/Rollback" ? "nav-item " : "nav-item"}>
                                <Link to="/Rollback" className={location.pathname == "/Rollback" ? "nav-link active" : "nav-link"} >Rollback</Link>
                            </li>
                            <li className={location.pathname == "/Codetranslation" ? "nav-item " : "nav-item"}>
                                <Link to="/Codetranslation" className={location.pathname == "/Codetranslation" ? "nav-link active" : "nav-link"} >Code Translation</Link>
                            </li>
                        </ul>
                        <ul className="custom-navbar-nav navbar-nav ms-auto mb-2 mb-md-0">
                            <li className="nav-item rounded">
                                <a className="nav-link" href="#" role="button" data-bs-toggle="dropdown">{firstname}<img src={Usericon} className="ms-2" style={{ width: "20px", backgroundColor: "white" }}></img></a>
                                <ul className="dropdown-menu dropdown-menu-end" aria-labelledby="navbarDropdown">
                                    <li className={location.pathname == "/Usersetting" ? "dropdown-item" : "dropdown-item"}>
                                        <Link to="/Usersetting" className={location.pathname == "/Usersetting" ? "dropdown-item nav-link2 text-black bg-light active" : "dropdown-item nav-link2"}>User Setting</Link>
                                    </li>
                                    <li className={location.pathname == "/Configuration" ? "dropdown-item" : "dropdown-item"}>
                                        <Link to="/Configuration" className={location.pathname == "/Configuration" ? "dropdown-item nav-link2 text-black bg-light active" : "dropdown-item nav-link2"}>Configuration</Link>
                                    </li>
                                    <li className="dropdown-item">
                                    <button className="dropdown-item" onClick={handleLogout}>
                                     Logout
                                    </button>
                                    </li>
                                </ul>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav >
        </div >

    </>
    );
}
export default Header;