import axios from 'axios';
import { useEffect, useState } from "react";
import { useNavigate, Link } from 'react-router-dom';
import CryptoJS from 'crypto-js';
import secureLocalStorage from 'react-secure-storage';
import { useAuth } from '../Context/AuthContext';
import { ScaleLoader } from "react-spinners";
import withNetworkStatus from '../Component/NetworkStatusHOC';
import { } from '../CSS/custom.scss';
import { Zoom, ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Login() {
  const navigate = useNavigate();
  const { login } = useAuth();
  const [loading, setLoading] = useState(false);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [rememberMe, setRememberMe] = useState(false);
  const [usernameFocused, setUsernameFocused] = useState(false);
  const [passwordFocused, setPasswordFocused] = useState(false);

  useEffect(() => {
    const rememberMeData = secureLocalStorage.getItem('rememberMe');
    if (rememberMeData === 'true') {
      setRememberMe(true);
      const storedUsername = secureLocalStorage.getItem('username');
      setUsername(storedUsername);
      const storedPassword = secureLocalStorage.getItem('password');
      setPassword(storedPassword);
    }
  }, []);

  const handleUsernameChange = (event) => {
    setUsername(event.target.value);
  };

  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
  };

  const handleRememberMeChange = (event) => {
    setRememberMe(event.target.checked);
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    setLoading(true);
    await GetUser(username, password);
  };

  async function Encrypt(password) {
    const key = CryptoJS.enc.Utf8.parse('GHYZXFZH2A4B6N6P7R9SPNMT');
    const encrypted = CryptoJS.TripleDES.encrypt(password, key, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7,
    });
    return encrypted.toString();
  }

  async function GetUser(username, password) {
    try {
      const devbaseURL = process.env.REACT_APP_devbaseURL;
      const uatbaseURL = process.env.REACT_APP_uatbaseURL;
      const localbaseURL = process.env.REACT_APP_localbaseURL;
      const encryptedPassword = await Encrypt(password);
      // const login_result = await axios.post(`${uatbaseURL}/login`, {
      // user_name: username,
      // password: encryptedPassword,
      // });

      const login_result = await axios.post(`${devbaseURL}/login`, {
      user_name: username,
      password: encryptedPassword,
      });

      // const login_result = await axios.post(`${localbaseURL}/login`, {
      //   user_name: username,
      //   password: encryptedPassword,
      // });

      const id = login_result.data.data[0].user_id;
      const usertype = login_result.data.data[0].user_type;
      const firstname = login_result.data.data[0].first_name;
      const lastname = login_result.data.data[0].last_name;
      if (id) {
        if (login_result.data.status) {
          if (login_result.data.auth) {
            secureLocalStorage.setItem('Token', login_result.data.auth);
            // const token_result = await axios.put(`${uatbaseURL}/put_token`, {
            //   user_id: id,
            //   token: login_result.data.auth,
            // });

            const token_result = await axios.put(`${devbaseURL}/put_token`, {
              user_id: id,
              token: login_result.data.auth,
            });

            // const token_result = await axios.put(`${localbaseURL}/put_token`, {
            //   user_id: id,
            //   token: login_result.data.auth,
            // });
            if (token_result.data.status) {
              login(username, id, usertype, firstname, lastname);
              if (rememberMe) {
                secureLocalStorage.setItem('username', username);
                secureLocalStorage.setItem('password', password);
                secureLocalStorage.setItem('rememberMe', 'true');
              }
              navigate('/Home');
            }
          }
        } else {
          toast.error(login_result.data.message, { className: 'error-toast' });
        }
      }
    } catch (error) {
      toast.error("Incorrect username or password. Please try again.", { className: 'error-toast' });
    } finally {
      setLoading(false);
    }
  }

  return (
    <div className="container-fluid">
      <ToastContainer
        position="bottom-center"
        autoClose={3000}
        transition={Zoom}
      />
      <div className="d-flex flex-column min-vh-100 justify-content-center align-items-center">
        <div className="border border-secondary rounded bg w-25">
          <h1 className="display-5 text-center text-primary ">Exocube</h1>
          <hr />
          <h4 className="text-center">
            <small>Login to Exocube Framework</small>
          </h4>
          <br />
          <form onSubmit={handleSubmit}>
            <div className="row">
              <div className="col-sm-1">
              </div>
              <div className="col-sm-10">
                <div className="text-input">
                  <label className={usernameFocused || username !== "" ? "label-focus" : ""}>Username</label>
                  <input className={usernameFocused || username !== "" ? "input-focus" : ""} type="text" value={username} pattern=".{5,64}" required title="5 to 64 characters minimum" onFocus={() => setUsernameFocused(true)} onBlur={() => setUsernameFocused(false)} onChange={handleUsernameChange} />
                </div>
              </div>
              <div className="col-sm-1">
              </div>
            </div>
            <br />
            <div className="row">
              <div className="col-sm-1">
              </div>
              <div className="col-sm-10">
                <div className="text-input">
                  <label className={passwordFocused || password !== "" ? "label-focus" : ""}>Password</label>
                  <input className={passwordFocused || password !== "" ? "input-focus" : ""} type="password" value={password} pattern=".{5,64}" onFocus={() => setPasswordFocused(true)} onBlur={() => setPasswordFocused(false)} required title="5 to 64 characters minimum" onChange={handlePasswordChange} />
                </div>
              </div>
              <div className="col-sm-1">
              </div>
            </div>
            <br></br>
            <div className="row">
              <div className="col-sm-11">
                <div className="float-end">
                  <Link className="form-link-label" to='/Forgotpassword'><small>Forgot Password?</small></Link>
                </div>
              </div>
              <div className="col-sm-1">
              </div>
            </div>
            <div className="row">
              <div className="col-sm-1">
              </div>
              <div className="col-sm-10">
                <div className="form-check">
                  <input type="checkbox" className="form-check-input" id="rememberchekbox" name="option2" checked={rememberMe} onClick={handleRememberMeChange} />
                  <label className="form-check-label" htmlFor="rememberchekbox"><small>Remember Me</small></label>
                </div>
              </div>
              <div className="col-sm-1">
              </div>
            </div>

            <br />
            <div className="row">
              <label className="text-center text-danger"></label>
            </div>
            <div className="row">
              <div className="col-sm-1">
              </div>
              <div className="col-sm-10">
                <div className="d-grid">
                  <button className="btn btn-primary btn-block" type="submit">
                    {loading ? (
                      // Display the spinner when loading is true
                      <ScaleLoader height={10} width={5} color={"#fff"} loading={loading} />
                    ) : (
                      // Display "Log In" when loading is false
                      "Log In"
                    )}
                  </button>
                </div>
              </div>
              <div className="col-sm-1">
              </div>
            </div>
            <br />
            <br />
          </form>
        </div>
      </div>
    </div>
  );
}

export default withNetworkStatus(Login);
