import React, { useState, useEffect } from 'react';
import NetworkStatusMessage from './NetworkStatusMessage';

const withNetworkStatus = (WrappedComponent) => {
  const NetworkStatusWrapper = (props) => {
    const [online, setOnline] = useState(navigator.onLine);

    useEffect(() => {
      const handleOnline = () => setOnline(true);
      const handleOffline = () => setOnline(false);

      window.addEventListener('online', handleOnline);
      window.addEventListener('offline', handleOffline);

      return () => {
        window.removeEventListener('online', handleOnline);
        window.removeEventListener('offline', handleOffline);
      };
    }, []);

    return (
      <div>
        {!online && <NetworkStatusMessage />}
        {online && <WrappedComponent {...props} />}
      </div>
    );
  };

  return NetworkStatusWrapper;
};

export default withNetworkStatus;
