import React, { useState, useEffect } from "react";
import Header from "./Header";
import CryptoJS from 'crypto-js';
import axiosInstance from './axios-interceptor.js';
import TableLoader from '../Component/TableLoader.js'
import { ScaleLoader, PuffLoader } from "react-spinners";
import { jwtDecode } from 'jwt-decode';
import { useNavigate } from 'react-router-dom';
import secureLocalStorage from 'react-secure-storage';
import withNetworkStatus from '../Component/NetworkStatusHOC';
import AlertModel from "../Component/AlertModel";
import DataTableModel from '../Component/DataTable';
import { useAuth } from '../Context/AuthContext';
import { Zoom, ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Rollback() {
  const { usertype, username } = useAuth();
  const { id } = useAuth();
  const [formValid, setFormValid] = useState(false);
  const [buttonClicked, setButtonClicked] = useState(false);
  const [tableVersions, setTableVersions] = useState([]);
  const [showTable, setShowTable] = useState(false);
  const [clusterId, setClusterId] = useState('');
  const [clusterName, setClusterName] = useState(null);
  const [selectedSolution, setSelectedSolution] = useState('');
  const [selectedCatalog, setSelectedCatalog] = useState('');
  const [selectedSchema, setSelectedSchema] = useState('');
  const [selectedTable, setSelectedTable] = useState('');

  const [targetdatabase, setTargetDatabase] = useState([]);
  const [catalog, setCatalog] = useState([]);
  const [sourceschema, setSourceSchema] = useState([]);
  const [sourcetable, setSourceTable] = useState([]);
  const [version, setVersion] = useState(null);
  const [datetime, setDateTime] = useState(null);
  const [removeMetadata, setRemoveMetadata] = useState(false);
  const [rollbackBronzeTable, setRollbackBronzeTable] = useState(false);
  const [rollbackSilverTable, setRollbackSilverTable] = useState(false);
  const [showModal_Poprollback_restore, setShowModal_Poprollback_restore] = useState(false);
  const [showModal_Popstcluster, setShowModal_Popstcluster] = useState(false);
  const [loading, setLoading] = useState(false);
  const [issdLoading, setSdLoading] = useState(false);
  const [isscsLoading, setSCSLoading] = useState(false);
  const [isssLoading, setSsLoading] = useState(false);
  const [isstLoading, setStLoading] = useState(false);
  const [issbLoading, setSbLoading] = useState(false);
  const [isrmLoading, setisrmLoading] = useState(false);
  const [targettableFocused, setTargetTableFocused] = useState(false);
  const [targetchemaFocused, setTargetSchemaFocused] = useState(false);
  const [catalogFocused, setCatalogFocused] = useState(false);
  const [targetdatabaseFocused, setTargetDatabaseFocused] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalrecords, setTotalRecords] = useState(0);
  const itemsPerPage = 5;
  const totalPages = Math.ceil(totalrecords / itemsPerPage);

  const navigate = useNavigate()

  const CheckToken = async () => {
    if (!secureLocalStorage.getItem('Token')) {
      return false;
    } else {
      const decoded = jwtDecode(secureLocalStorage.getItem('Token'));
      const currentTimestamp = Math.floor(Date.now() / 1000);

      if (decoded.exp < currentTimestamp) {
        return false;
      } else {
        const display_result = await axiosInstance.get("get_token", {
          params: {
            user_id: id
          }
        });
        if (display_result.data.status) {
          const newToken = secureLocalStorage.getItem('Token');
          const storedtoken = display_result.data.data[0].token;
          if (newToken !== storedtoken) {
            return false;
          } else {
            return true;
          }
        }
      }
    }
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (formValid) {
      await BtnSaveClick(e);
    }
  };

  useEffect(() => {
    (async () => {
      let chk_token = await CheckToken();
      if (chk_token) {
        await Promise.all([TargetDatabase()]);
        if (selectedSolution && selectedCatalog && selectedSchema && selectedTable) {
          setFormValid(true);
          if (buttonClicked) {
            await Promise.all([Load()]);
          }
        } else {
          setFormValid(false);
        }

      } else {
        navigate('/')
      }
    })();
  }, [buttonClicked, currentPage, selectedSolution, selectedCatalog, selectedSchema, selectedTable]);


  async function counttotalrecords(username, token, hostname, full_name) {
    const display_result = await axiosInstance.get("get_databricks_count_versions", {
      params: {
        dbtname: full_name,
        secrect: token,
        serverHostname: hostname,
        httpPath: username,
      }
    });
    if (display_result.data.status) {
      setTotalRecords(display_result.data.data[0].db_ver_count);
    }
  }

  async function Load() {
    try {
      let chk_token = await CheckToken();
      if (chk_token) {
        const res = await ConnectionString(selectedSolution);
        const username = await Decrypt(res.data.data[0].username);
        const token = await Decrypt(res.data.data[0].token);
        const hostname = await Decrypt(res.data.data[0].hostname);
        const full_name = selectedCatalog + "." + selectedSchema + "." + selectedTable;
        await counttotalrecords(username, token, hostname, full_name);
        const display_result = await axiosInstance.get("get_databricks_history_detail",
          {
            params: {
              dbtname: full_name,
              current: (currentPage - 1) * itemsPerPage,
              pagesize: itemsPerPage,
              secrect: token,
              serverHostname: hostname,
              httpPath: username,
            }

          });

        if (display_result.data.status) {
          setTableVersions(display_result.data.data);
          setSbLoading(false);
          setLoading(false);
          setShowTable(true);
        } else {
          //console.log("err", display_result);
          setLoading(false);
          setSbLoading(false);
        }
      } else {
        navigate('/')
      }
    }
    catch (error) {
      toast.error("Display Rollback Data Failed", { className: 'error-toast' });
      setLoading(false);
      setSbLoading(false);
    }
  }

  async function BtnSaveClick(e) {
    e.preventDefault();
    setCurrentPage(1);
    setButtonClicked(true);
    setShowTable(false);
    setSbLoading(true);
    setLoading(true);
  };

  async function TargetDatabase() {
    const display_result = await axiosInstance.get("get_target_database_name", {
    });
    if (display_result.data.status) {
      setTargetDatabase(display_result.data.data);
    }
  };


  async function ConnectionString(database_name) {
    setSelectedSolution(database_name)
    const display_result = await axiosInstance.get("get_connection_string", {
      params: {
        user_name: username,
        database_name: database_name
      }
    });
    return display_result
  };

  const handlePageChange = (newPage) => {
    if (newPage >= 1 && newPage <= totalPages) {
      setCurrentPage(newPage);
    }
  };

  async function handleSolutionChange(db_name) {
    let chk_token = await CheckToken();
    if (chk_token) {
      setShowTable(false);
      get_db_cluster_id(db_name);
    } else {
      navigate('/')
    }
  };

  async function handleCatalogChange(cat_name) {
    setSelectedCatalog('');
    setSourceSchema([]);
    setSelectedSchema('');
    setSourceTable([]);
    setSelectedTable('');
    let chk_token = await CheckToken();
    if (chk_token) {
      setShowTable(false);
      setSsLoading(true);
      setSelectedCatalog(cat_name);
      const res = await ConnectionString(selectedSolution);

      const username = await Decrypt(res.data.data[0].username);
      const token = await Decrypt(res.data.data[0].token);
      const hostname = await Decrypt(res.data.data[0].hostname);

      const display_result = await axiosInstance.get("get_databricks_schema_name", {
        params: {
          catalog: cat_name,
          secrect: token,
          serverHostname: hostname,
          httpPath: username,
        }
      });
      if (display_result.data.status) {
        setSourceSchema(display_result.data.data);
      }
      setSsLoading(false);
    } else {
      navigate('/')
    }
  };

  async function handleSchemaChange(schema_name) {
    setSourceTable([]);
    setSelectedTable('');
    let chk_token = await CheckToken();
    if (chk_token) {
      setShowTable(false);
      setStLoading(true);
      setSelectedSchema(schema_name);
      const res = await ConnectionString(selectedSolution);

      const username = await Decrypt(res.data.data[0].username);
      const token = await Decrypt(res.data.data[0].token);
      const hostname = await Decrypt(res.data.data[0].hostname);

      const display_result = await axiosInstance.get("get_databricks_table_name", {
        params: {
          catalog: selectedCatalog,
          schema_name: schema_name,
          secrect: token,
          serverHostname: hostname,
          httpPath: username,
        }
      });
      if (display_result.data.status) {
        setSourceTable(display_result.data.data);
      }
      setStLoading(false);
    } else {
      navigate('/')
    }
  };

  const handleTableChange = (selectedValue) => {
    setButtonClicked(false);
    setShowTable(false);
    setSelectedTable(selectedValue);
  };

  async function Bronze() {
    let chk_token = await CheckToken();
    if (chk_token) {
      const res = await ConnectionString(selectedSolution);
      const username = await Decrypt(res.data.data[0].username);
      const token = await Decrypt(res.data.data[0].token);
      const hostname = await Decrypt(res.data.data[0].hostname);
      const bronze_full_name = selectedCatalog + "." + selectedSchema + "." + selectedTable;

      const update_bronze_result = await axiosInstance.put("put_databricks_rollback_table",
        {
          dbtname: bronze_full_name,
          version: version,
          secrect: token,
          serverHostname: hostname,
          httpPath: username

        });
      if (update_bronze_result.data.status) {
        toast.success(update_bronze_result.data.message, { className: 'success-toast' });
      } else {
        toast.error(update_bronze_result.data.message, { className: 'error-toast' });
      }
    } else {
      navigate('/')
    }
  }

  async function Silver() {
    let chk_token = await CheckToken();
    if (chk_token) {
      const res = await ConnectionString(selectedSolution);
      const username = await Decrypt(res.data.data[0].username);
      const token = await Decrypt(res.data.data[0].token);
      const hostname = await Decrypt(res.data.data[0].hostname);
      const silver_full_name = selectedCatalog + ".silver." + selectedTable;

      const update_silver_result = await axiosInstance.put("put_databricks_rollback_table",
        {
          dbtname: silver_full_name,
          version: version,
          secrect: token,
          serverHostname: hostname,
          httpPath: username

        });
      if (update_silver_result.data.status) {
        toast.success(update_silver_result.data.message, { className: 'success-toast' });
      } else {
        toast.error(update_silver_result.data.message, { className: 'error-toast' });
      }
    } else {
      navigate('/')
    }
  }

  async function handleRollbackRestoreClick() {
    let chk_token = await CheckToken();
    if (chk_token) {
      setisrmLoading(true);
      if (removeMetadata) {
        const bronze_time = datetime;
        const catalog = process.env.REACT_APP_development;
        const res = await ConnectionString(selectedSolution);
        const username = await Decrypt(res.data.data[0].username);
        const token = await Decrypt(res.data.data[0].token);
        const hostname = await Decrypt(res.data.data[0].hostname);
        let process_ids = "";
        const dateTimeWithoutAmPm = bronze_time.replace(/\b(?:AM|PM)\b/g, '');

        const display_result = await axiosInstance.get("get_databricks_metadata", {
          params: {
            catalog: catalog,
            dbtname: selectedTable,
            bronzetime: dateTimeWithoutAmPm,
            secrect: token,
            serverHostname: hostname,
            httpPath: username,
          }
        });
        let process_id_list = display_result.data.data;
        const pIds = process_id_list.map(item => item.process_id);
        process_ids = pIds.join(',');
        if (process_ids) {
          if (display_result.data.status) {
            const update_result = await axiosInstance.delete("delete_databricks_metadata",
              {
                data: { catalog: catalog, process_ids: process_ids, secrect: token, serverHostname: hostname, httpPath: username }

              });
            if (update_result.data.status) {
              toast.success(update_result.data.message, { className: 'success-toast' });
            } else {
              toast.error(update_result.data.message, { className: 'error-toast' });
            }
          }

        }
        const update_mysql_result = await axiosInstance.delete("delete_mysql_metadata",
          {
            data: { tbl_name: selectedTable, bronzetime: dateTimeWithoutAmPm }

          });
        if (update_mysql_result.data.status) {
          toast.success(update_mysql_result.data.message, { className: 'success-toast' });
        } else {
          toast.error(update_mysql_result.data.message, { className: 'error-toast' });
        }
        if (selectedSchema === 'bronze') {

          if (rollbackSilverTable) {
            await Bronze();
            await Silver();
          } else {
            await Bronze();
          }
        } else if (selectedSchema === 'silver') {

          if (rollbackBronzeTable) {
            await Bronze();
            await Silver();
          } else {
            await Silver();
          }

        }
      }
      else if (selectedSchema === 'bronze') {

        if (rollbackSilverTable) {
          await Bronze();
          await Silver();

        } else {
          await Bronze();
        }
      } else if (selectedSchema === 'silver') {

        if (rollbackBronzeTable) {
          await Bronze();
          await Silver();

        } else {
          await Silver();
        }
      } else {
        //console.log('Selected schema is neither bronze nor silver');
      }
      setisrmLoading(false);
      closeModal_Poprollback_restore();
      await Load();
    } else {
      navigate('/')
    }
  };

  const openModal_Popstcluster = () => {
    setShowModal_Popstcluster(true);
  };

  const closeModal_Popstcluster = () => {
    setShowModal_Popstcluster(false);
  };
  const openModal_Poprollback_restore = (version, date_time) => {
    setShowModal_Poprollback_restore(true);
    setVersion(version);
    setDateTime(date_time);
  };

  const closeModal_Poprollback_restore = () => {
    setShowModal_Poprollback_restore(false);
  };
  const handleClusterStartClick = async () => {
    let chk_token = await CheckToken();
    if (chk_token) {
      //event.preventDefault();
      setSCSLoading(true);
      const response = await axiosInstance.post("post_start_cluster", {
        cluster_id: clusterId,
      });
      if (response) {
        await get_cluster_status(clusterId, selectedSolution);
      }
    } else {
      navigate('/')
    }
  }

  async function Decrypt(encryptedPassword) {
    const key = CryptoJS.enc.Utf8.parse('GHYZXFZH2A4B6N6P7R9SPNMT');
    const decrypted = CryptoJS.TripleDES.decrypt(
      {
        ciphertext: CryptoJS.enc.Base64.parse(encryptedPassword),
      },
      key,
      {
        mode: CryptoJS.mode.ECB,
        padding: CryptoJS.pad.Pkcs7,
      }
    );
    return decrypted.toString(CryptoJS.enc.Utf8);
  }

  const get_db_cluster_id = async (db_name) => {
    let chk_token = await CheckToken();
    setSelectedSolution(db_name);
    if (chk_token) {
      setSdLoading(true);
      const display_result = await axiosInstance.get("get_rollback_cluster_id", {
      });
      if (display_result.data) {
        const result = display_result.data;
        const cluster_id = result.settings.existing_cluster_id;
        setClusterId(cluster_id);
        await get_cluster_status(cluster_id, db_name);
      }
    } else {
      navigate('/')
    }
  };

  const get_cluster_status = async (cluster_id, db_name) => {
    let chk_token = await CheckToken();
    if (chk_token) {
      const display_results = await axiosInstance.get("get_cluster_detail", {
        params: {
          cluster_id: cluster_id,
        }
      });
      if (display_results.data) {
        const json_value = display_results.data;
        setClusterName(json_value.cluster_name);
        if (json_value.state === "PENDING") {
          setTimeout(() => {
            get_cluster_status(cluster_id, db_name);
          }, 2000);
        }
        else if (json_value.state === "RUNNING") {
          let res = "";
          if (selectedSolution === "") {
            res = await ConnectionString(db_name);
          } else {
            res = await ConnectionString(selectedSolution);
          }

          const username = await Decrypt(res.data.data[0].username);
          const hostname = await Decrypt(res.data.data[0].hostname);
          let chk_token = res.data.data[0].token;
          if (chk_token) {
            const token = await Decrypt(res.data.data[0].token);
            const display_result = await axiosInstance.get("get_databricks_catalog_name", {
              params: {
                secrect: token,
                serverHostname: hostname,
                httpPath: username,
              }
            });
            if (display_result.data.message) {
              toast.warning(display_result.data.message, { className: 'warning-toast' });
              setSdLoading(false);
            } else {
              if (display_result.data.status) {
                setCatalog(display_result.data.data);
              }
              closeModal_Popstcluster();
              setSCSLoading(false);
              setSdLoading(false);
            }
          } else {
            toast.warning(`Add token in ${db_name} connection`, { className: 'warning-toast' });
            setSdLoading(false);
          }
        }
        else if (json_value.state === "TERMINATED") {
          openModal_Popstcluster();
          setSdLoading(false);
        }
      } else {
        //console.error("Data is not an array:", display_results.data);
      }
    } else {
      navigate('/')
    }
  }

  return (
    <div>
      <Header></Header>
      <div className="mt-5 p-2 ms-1">
        <ToastContainer
          position="bottom-center"
          autoClose={3000}
          transition={Zoom}
        />
        <div class="container-fluid">
          <br />
          <form onSubmit={handleSubmit}>
            <div class="container border">
              <div class="row mt-2  ">
                <div class="input-group input-control-sm justify-content-center ">

                  <div class="form-group form-control-sm" style={{ width: 200, position: 'relative' }}>
                    <div className="text-input">
                      <label className={targetdatabaseFocused || selectedSolution !== "" ? "label-focus" : ""} htmlFor="ddtargetsolution">
                        Target Solution
                      </label>
                      <div className="select-wrapper">
                        <select
                          className={`form-control form-control-sm select-input ${targetdatabaseFocused || selectedSolution !== "" ? "input-focus" : ""} `}
                          id="ddtargetsolution"
                          name="ddtargetsolution"
                          onChange={(event) => handleSolutionChange(event.target.value)}
                          onBlur={() => setTargetDatabaseFocused(false)}
                          onFocus={() => setTargetDatabaseFocused(true)}
                          required
                          autoComplete="off"
                          value={selectedSolution}
                        >
                          <option value="" hidden></option>
                          {targetdatabase.map((targetDatabase) => (
                            <option key={targetDatabase.database_type} value={targetDatabase.database_name}>
                              {targetDatabase.database_name}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="form-group form-control-sm" style={{ width: 200, position: 'relative' }}>
                    <div className="text-input">
                      <label className={catalogFocused || selectedCatalog !== "" ? "label-focus" : ""} htmlFor="ddcatalog">
                        Catalog
                      </label>
                      {issdLoading && (
                        <div style={{ position: 'absolute', top: '50%', right: '8px', transform: 'translateY(-50%)' }}>
                          <PuffLoader size={21} color={"#2E67F8"} loading={issdLoading} />
                        </div>
                      )}
                      <div className="select-wrapper">
                        <select
                          className={`form-control form-control-sm select-input ${catalogFocused || selectedCatalog !== "" ? "input-focus" : ""} `}
                          id="ddcatalog"
                          onChange={(event) => handleCatalogChange(event.target.value)}
                          onBlur={() => setCatalogFocused(false)}
                          onFocus={() => setCatalogFocused(true)}
                          value={selectedCatalog}
                          required
                        >
                          <option value="" hidden></option>
                          {catalog.map((Catalog) => (
                            <option key={Catalog.catalog} value={Catalog.catalog}>
                              {Catalog.catalog}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                  </div>

                  <div class="form-group form-control-sm" style={{ width: 200, position: 'relative' }}>
                    <div className="text-input">
                      <label className={targetchemaFocused || selectedSchema !== "" ? "label-focus" : ""} htmlFor="ddtargetschemaname">
                        Target Schema
                      </label>
                      {isssLoading && (
                        <div style={{ position: 'absolute', top: '50%', right: '8px', transform: 'translateY(-50%)' }}>
                          <PuffLoader size={21} color={"#2E67F8"} loading={isssLoading} />
                        </div>
                      )}
                      <div className="select-wrapper">
                        <select
                          className={`form-control form-control-sm select-input ${targetchemaFocused || selectedSchema !== "" ? "input-focus" : ""} `}
                          id="ddtargetschemaname"
                          onChange={(event) => handleSchemaChange(event.target.value)}
                          onBlur={() => setTargetSchemaFocused(false)}
                          onFocus={() => setTargetSchemaFocused(true)}
                          value={selectedSchema}
                          required
                        >
                          <option value="" hidden></option>
                          {sourceschema.map((schema) => (
                            <option key={schema.databaseName} value={schema.databaseName}>
                              {schema.databaseName}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                  </div>

                  <div class="form-group form-control-sm" style={{ width: 200 }}>
                    <div className="text-input">
                      <label className={targettableFocused || selectedTable !== "" ? "label-focus" : ""} htmlFor="ddsourcedatabase">Target Table</label>
                      {isstLoading && (
                        <div style={{ position: 'absolute', top: '50%', right: '8px', transform: 'translateY(-50%)' }}>
                          <PuffLoader size={21} color={"#2E67F8"} loading={isstLoading} />
                        </div>
                      )}
                      <div className="select-wrapper">
                        <select
                          className={`form-control form-control-sm select-input ${targettableFocused || selectedTable !== "" ? "input-focus" : ""} `}
                          id="ddtargettablename"
                          onChange={(event) => handleTableChange(event.target.value)}
                          onBlur={() => setTargetTableFocused(false)}
                          onFocus={() => setTargetTableFocused(true)}
                          value={selectedTable}
                          required>
                          {(
                            <option value="" hidden></option>
                          )}
                          {sourcetable.map((table) => (
                            <option key={table.tableName} value={table.tableName}>
                              {table.tableName}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                  </div>
                  <div class="form-group form-control-sm mt-1">
                    <button
                      type="submit"
                      id="btnsubmit"
                      className="btn btn-outline-primary btn-sm btn-block"
                    >
                      {issbLoading ? (
                        // Display the spinner when loading is true
                        <ScaleLoader height={10} width={5} color={"#2E67F8"} loading={issbLoading} />
                      ) : (
                        // Display "Log In" when loading is false
                        "Submit"
                      )}
                    </button>
                  </div>
                  <div class="form-group form-control-sm d-grid mt-1">
                    <label CssClass="text-danger" id="lblrunerror" ></label>
                  </div>
                </div>
              </div>
            </div>
          </form>
          <br />
          {(usertype === process.env.REACT_APP_ADMIN || usertype === process.env.REACT_APP_EDITOR) && (
            <div>
              {loading ? (
                // Display the spinner when loading is true
                <TableLoader loading={loading} />
              ) : (
                null
              )}
              {tableVersions && tableVersions.length > 0 && (
                <DataTableModel
                  showTable={showTable}
                  headings={tableVersions.length > 0 ? [
                    ...Object.keys(tableVersions[0]).map((key, index) => (
                      <th key={index}>{key}</th>
                    )),
                    <th key="Actions">Actions</th>
                  ] : []}
                  items={tableVersions}
                  removeItems={["Version", "Date Time"]}
                  rollbackActions={openModal_Poprollback_restore}
                  currentPage={currentPage}
                  totalPages={totalPages}
                  prevPage={() => handlePageChange(currentPage - 1)}
                  currPage={handlePageChange}
                  nxtPage={() => handlePageChange(currentPage + 1)}
                />
              )}
            </div>
          )}
          {(usertype === process.env.REACT_APP_ANALYST) && (
            <div>
              {loading ? (
                // Display the spinner when loading is true
                <TableLoader loading={loading} />
              ) : (
                null
              )}
              {tableVersions && tableVersions.length > 0 && (
                <DataTableModel
                  showTable={showTable}
                  headings={tableVersions.length > 0 ? [
                    ...Object.keys(tableVersions[0]).map((key, index) => (
                      <th key={index}>{key}</th>
                    )),
                  ] : []}
                  items={tableVersions}
                  removeItems={false}
                  currentPage={currentPage}
                  totalPages={totalPages}
                  prevPage={() => handlePageChange(currentPage - 1)}
                  currPage={handlePageChange}
                  nxtPage={() => handlePageChange(currentPage + 1)}
                />
              )}
            </div>
          )}
        </div>

        <div id="Poprollback_restore" className={`modal fade ${showModal_Poprollback_restore ? 'show' : ''}`} role="dialog" style={{ display: showModal_Poprollback_restore ? 'block' : 'none' }}>
          <div class="modal-dialog">
            <div class="modal-content">
              <div class="modal-header">
                <h4 class="modal-title">Rollback Table Version</h4>
                <button type="button" class="btn-close" data-bs-dismiss="modal" data-dismiss="modal" onClick={closeModal_Poprollback_restore}></button>
              </div>
              <div class="modal-body ">
                <div class="container-sm border">
                  <div class="row">
                    <div class="col-sm-12">
                      <h5>
                        <label ID="lblrestore" />Are you sure you want to Restore <b>{version}</b> Version?</h5>
                      <span id="Spanrestoremsg"></span>
                    </div>
                  </div>
                  <br />
                  <div class="row mb-3">
                    <div class="col-sm-2"></div>
                    <div class="col-sm-10">
                      <label>
                        <input
                          type="checkbox"
                          id="chkmetadata"
                          checked={removeMetadata}
                          onChange={() => setRemoveMetadata(!removeMetadata)}
                        />
                        Remove metadata.
                      </label>
                      {selectedSchema === 'bronze' && (
                        <>
                          <div className="p-1"></div>
                          <label>
                            <input
                              type="checkbox"
                              id="chksilvertable"
                              checked={rollbackSilverTable}
                              onChange={() => setRollbackSilverTable(!rollbackSilverTable)}
                            />
                            Rollback Silver table.
                          </label>
                        </>
                      )}
                      {selectedSchema === 'silver' && (
                        <>
                          <div className="p-1"></div>
                          <label>
                            <input
                              type="checkbox"
                              id="chkbronzetable"
                              checked={rollbackBronzeTable}
                              onChange={() => setRollbackBronzeTable(!rollbackBronzeTable)}
                            />
                            Rollback Bronze table.
                          </label>
                        </>
                      )}
                    </div>
                  </div>
                  <br />
                </div>
              </div>
              <div class="modal-footer">
                <div class="form-group form-control-sm d-grid">
                  <button
                    type="button"
                    id="btnrollback_restore"
                    onClick={handleRollbackRestoreClick}
                    className="btn btn-outline-danger btn-sm btn-block"
                  >
                    {isrmLoading ? (
                      // Display the spinner when loading is true
                      <ScaleLoader height={10} width={5} color={"#DC3545"} loading={isrmLoading} />
                    ) : (
                      "Rollback"
                    )}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>

        <AlertModel
          modelTitle={"Cluster"}
          lblName={<>start <b>{clusterName}</b> Cluster ?</>}
          showModal={showModal_Popstcluster}
          closeModal={closeModal_Popstcluster}
          isalLoading={isscsLoading}
          saveClick={handleClusterStartClick}
          btnname={"Start"}
          btncolor={"#2E67F8"}
          btnclass={"btn btn-outline-primary btn-sm btn-block"}
        />

      </div>
    </div>
  );
}

export default withNetworkStatus(Rollback);