import Header from "./Header";
import React, { useEffect } from "react";
import secureLocalStorage from 'react-secure-storage';
import { useNavigate } from 'react-router-dom';
import { jwtDecode } from 'jwt-decode';
import withNetworkStatus from '../Component/NetworkStatusHOC';
import { useAuth } from '../Context/AuthContext';
import axiosInstance from './axios-interceptor.js';

function Home() {
    const navigate = useNavigate()
    const { id } = useAuth();

    useEffect(() => {
        const fetchData = async () => {
            if (!secureLocalStorage.getItem('Token')) {
                navigate('/')
                return;
            } else {
                const decoded = jwtDecode(secureLocalStorage.getItem('Token'));
                const currentTimestamp = Math.floor(Date.now() / 1000);

                if (decoded.exp < currentTimestamp) {
                    navigate('/')
                    return;
                } else {
                    const display_result = await axiosInstance.get("get_token", {
                        params: {
                            user_id: id
                        }
                    });
                    if (display_result.data.status) {
                        const newToken = secureLocalStorage.getItem('Token');
                        const storedtoken = display_result.data.data[0].token;
                        if (newToken !== storedtoken) {
                            navigate('/')
                            return;
                        }
                    }
                }
            }
        };

        fetchData(); // Call the function

    }, [navigate, id]); // Add dependencies to the dependency array


    return (
        <div>
            <Header></Header>
            <div className="mt-5">
                <div class="p-5 bg-primary text-white ">
                    <h6 class="display-6 text-center"><small>Welcome to the Exocube Inc.</small></h6>
                    <p class="display-1 text-center">Reconciliation Framework</p>
                </div>

                <div class="mt-3 mb-3 p-5 me-5">
                    <h2 class="display-6 text-start">Data Engineering for AI</h2>
                    <div class="mt-3 p-1">
                        <h4 class="lead">Exocube is a Data for AI driven organization that helps companies transition to a Modern Data Architecture for Data Processing, Advanced Analytics and AI, in the cloud of your choosing.</h4>
                    </div>
                    <div class="mt-2 p-1">
                        <blockquote class="blockquote">
                            <p class="lead">As a Databricks Implementation Partner, we help remove barriers and make it easier for our customers to get started building and deploying a Data Lakehouse.   </p>
                            <p class="lead">We have extensive experience migrating organizations from legacy Data Warehouse environments into the latest modern data architecture, the Databricks Lakehouse, bringing together scalable data processing  workloads with Machine Learning and AI. </p>
                        </blockquote>
                    </div>
                </div>
                <div class="p-5 text-white bg-primary">
                    <div class="container">
                        <h6 class="display-6 text-center lead"><small>Modernize Your Legacy Data Platform</small></h6>
                        <br />
                        <blockquote class="blockquote">
                            <p class="text-center lead">Exocube can help your organization transition to a scalable, on-demand, cloud strategy for data processing using Databricks to enable your business to achieve faster access to information for improved competitive advantage.</p>
                        </blockquote>
                    </div>
                </div>

                <div class="p-5 text-black bg-light">
                    <div class="container">
                        <blockquote class="blockquote">
                            <p class="text-center lead">
                                Most Data Warehouses and Databases are not able to keep up with increasing business demand for more information. They are difficult to scale and have challenges providing data at the frequency the business needs.
                            </p>
                            <p class="text-center lead">Modernize your on-premise or cloud Data Warehouse to a Databricks Lakehouse architecture in any cloud, be it Microsoft Azure, Amazon AWS or Google GCP.</p>
                        </blockquote>
                    </div>
                </div>
            </div>

        </div>
    );
}

export default withNetworkStatus(Home);