const DisplaySmInformationModel = ({
    modelTitle,
    showModal,
    closeModal,
    key_1,
    key_2,
    key_3,
    key_4,
    val_1,
    val_2,
    val_3,
    val_4
}) => {
    return (
        <div id="Popsmallview" className={`modal fade ${showModal ? 'show' : ''}`} role="dialog" style={{ display: showModal ? 'block' : 'none' }} >
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <h4 className="modal-title">{modelTitle}</h4>
                        <button type="button" className="btn-close" data-bs-dismiss="modal" data-dismiss="modal" onClick={closeModal}></button>
                    </div>
                    <div className="modal-body ">
                        <div className="container-sm border">
                            {key_1 && val_1 &&
                                <div className="row">
                                    <div className="col-sm-5">
                                        <b>{key_1}</b>
                                    </div>
                                    <div className="col-sm-7">
                                        <p id="txt1" className="text-break">{val_1}</p>
                                    </div>
                                </div>
                            }
                            {key_2 && val_2 &&
                                <div className="row">
                                    <div className="col-sm-5">
                                        <b>{key_2}</b>
                                    </div>
                                    <div className="col-sm-7">
                                        <p id="txt2" className="text-break">{val_2}</p>
                                    </div>
                                </div>
                            }
                            {key_3 && val_3 &&
                                <div className="row">
                                    <div className="col-sm-5">
                                        <b>{key_3}</b>
                                    </div>
                                    <div className="col-sm-7">
                                        <p id="txt3" className="text-break">{val_3}</p>
                                    </div>
                                </div>
                            }
                            {key_4 && val_4 &&
                                <div className="row">
                                    <div className="col-sm-5">
                                        <b>{key_4}</b>
                                    </div>
                                    <div className="col-sm-7">
                                        <p id="txt4" className="text-break">{val_4}</p>
                                    </div>
                                </div>
                            }
                        </div>
                        <br />
                    </div>
                    <div className="modal-footer">
                    </div>
                </div>
            </div>
        </div>
    );
}

export default DisplaySmInformationModel;
