import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min';
import 'bootstrap-icons/font/bootstrap-icons.css'
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Login from './Pages/Login';
import Home from './Pages/Home';
import Sources from './Pages/Sources';
import Dataquality from './Pages/Dataquality';
import Rollback from './Pages/Rollback';
import Usersetting from './Pages/Usersetting';
import Configuration from './Pages/Configuration';
import Sidebarmenu from './Pages/Slidermenu';
import Datamigration_home from './Pages/Datamigration_home';
import Datamigration_metadata from './Pages/Datamigration_metadata';
import Datamigration_migratetable from './Pages/Datamigration_migratetable';
import Codetranslation from './Pages/Codetranslation';
import Forgotpassword from './Pages/Forgotpassword';
import { AuthProvider } from './Context/AuthContext';

function App() {
  return (
    <div className="App">
      <Router> {/* Added Router with basename */}
        <AuthProvider>
          <Routes>
            <Route path="/Home" element={<Home />} />
            <Route path="/" element={<Login />} />
            <Route path="/Sources" element={<Sources />} />
            <Route path="/Dataquality" element={<Dataquality />} />
            <Route path="/Datamigration_home" element={<Datamigration_home />} />
            <Route path="/Datamigration_metadata" element={<Datamigration_metadata />} />
            <Route path="/Datamigration_migratetable" element={<Datamigration_migratetable />} />
            <Route path="/Rollback" element={<Rollback />} />
            <Route path="/Codetranslation" element={<Codetranslation />} />
            <Route path="/Usersetting" element={<Usersetting />} />
            <Route path="/Configuration" element={<Configuration />} />
            <Route path="/Sidebarmenu" element={<Sidebarmenu />} />
            <Route path="/Forgotpassword" element={<Forgotpassword />} />
          </Routes>
        </AuthProvider>
      </Router>
    </div>
  );
}

export default App;
