import React, { useState, useRef } from "react";
import axiosInstance from './axios-interceptor.js';
import CryptoJS from 'crypto-js';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useAuth } from '../Context/AuthContext';
import secureLocalStorage from 'react-secure-storage';
import { } from '../CSS/custom.scss';
import { ScaleLoader } from "react-spinners";
import { Zoom, ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Forgotpassword() {
    const [username, setUsername] = useState('');
    const [userid, setUserid] = useState('');
    const [newpassword, setNewPassword] = useState('');
    const [confirmpassword, setConfirmPassword] = useState('');
    const [verifyotp, setVerifyOtp] = useState(['', '', '', '', '', '']);
    const [user_mail, setUser_Mail] = useState('');
    const [usernameFocused, setUsernameFocused] = useState(false);
    const [passwordFocused, setPasswordFocused] = useState(false);
    const [confirmpasswordFocused, setConfirmPasswordFocused] = useState(false);
    const inputRefs = useRef([]);
    const { login } = useAuth();
    const [loading, setLoading] = useState(false);
    const [vloading, setVLoading] = useState(false);
    const [sloading, setSLoading] = useState(false);

    const navigate = useNavigate();

    async function Encrypt(password) {
        const key = CryptoJS.enc.Utf8.parse('GHYZXFZH2A4B6N6P7R9SPNMT');
        const encrypted = CryptoJS.TripleDES.encrypt(password, key, {
            mode: CryptoJS.mode.ECB,
            padding: CryptoJS.pad.Pkcs7,
        });
        return encrypted.toString();
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        const Forgot_result = await axiosInstance.get("get_forgot_username_status", {
            params: {
                user_name: username,
            }
        });
        if (Forgot_result.data.status) {
            if (Forgot_result.data.data[0].user_name == 1) {
                toast.success("Successfully Found UserName", { className: 'success-toast' });
                await send_email(username);

            }
            else {
                toast.error("Not Found UserName", { className: 'error-toast' });
                setLoading(false);
            }
        } else {
            toast.error(Forgot_result.data.message, { className: 'error-toast' });
            setLoading(false);
        }
    }

    async function send_email(user_name) {
        const display_result = await axiosInstance.get("get_user_email_otp", {
            params: {
                user_name: user_name,
            }
        });
        if (display_result.data.status) {
           
            const userid = display_result.data.data[0].user_id;
            const useremail = display_result.data.data[0].email;
            setUserid(userid);

            const to = useremail;

            const otp = generateRandomNumber(100000, 999999);
            const update_result = await axiosInstance.put("update_user_otp", {
                otp: otp,
                email: useremail,
                user_id: userid,
            });
            if (update_result.data.status) {
                const response = await axiosInstance.post("send_email", {
                    to: to,
                    subject: 'Forgot Password',
                    body: `Hi ${username}, We received a request to reset the password for your account. Need to reset your password? Use your secret code: ${otp}. If you did not forget your password, you can ignore this email.`
                });

                if (response.status === 200) {

                    toast.success('Email sent successfully', { className: 'success-toast' });
                    const user_mail_sent = await maskEmail(useremail);
                    setUser_Mail(user_mail_sent);
                    const sid = "panelforgotpassword";
                    const tid = "panelverifyotp";
                    const element = document.getElementById(sid);
                    const txtelement = document.getElementById(tid);
                    if (element && txtelement) {
                        element.style.display = 'none'; // Set display to 'block' to show the element
                        txtelement.style.display = 'block'
                    }
                    setLoading(false);
                } else {
                    toast.error('Failed to send email', { className: 'error-toast' });
                }

            }

        }
    }

    const maskEmail = async (email) => {
        return new Promise((resolve, reject) => {
            const cntlen = email.length;
            const atIndex = email.indexOf('@');
            const output = email.substring(atIndex - 1);
            const outputlen = output.length;
            const l = cntlen - outputlen;
            const firstPart = email.substring(0, 1);
            const lastPart = email.substring(l, outputlen);
            const result = '*'.repeat(l);
            const maskedEmail = firstPart + result + lastPart;
            resolve(maskedEmail);
        });
    }

    async function handleverify(e) {
        e.preventDefault();
        setVLoading(true);
        const Confirm_otp = verifyotp.join('');
        const verify_result = await axiosInstance.get("get_verify_otp", {
            params: {
                user_id: userid,
            }
        });
        if (verify_result.data.status) {
            if (verify_result.data.data[0].otp === Confirm_otp) {
                const update_result = await axiosInstance.put("put_confirm_otp", {
                    otp: null,
                    user_id: userid,
                });
                if (update_result.data.status) {
                    toast.success("Confirm Successfully", { className: 'success-toast' });
                    setVLoading(false);
                    const vid = "panelchangepassword";
                    const eid = "panelverifyotp";
                    const velement = document.getElementById(vid);
                    const eelement = document.getElementById(eid);
                    if (velement && eelement) {
                        velement.style.display = 'block'; // Set display to 'block' to show the element
                        eelement.style.display = 'none';
                    }

                }
            }
            else {
                toast.error("Invalid OTP Number", { className: 'error-toast' });
                setVLoading(false);
            }
        } else {
            toast.error(verify_result.data.message, { className: 'error-toast' });
        }
    }

    async function handleSaveNewPassword(e) {
        e.preventDefault();
        setSLoading(true);
        const encryptpassword = await Encrypt(newpassword);
        const update_result = await axiosInstance.put("put_user_changenew_password", {
            password: encryptpassword,
            user_id: userid,
        });
        if (update_result.data.status) {
            toast.success("Updated Password Successfully", { className: 'success-toast' });
            await GetUser(username, newpassword)
        }
        else {
            toast.error("Invalid Password", { className: 'error-toast' });
            setSLoading(false);
        }
    }

    async function GetUser(username, password) {
        const encryptedPassword = await Encrypt(password);

        const login_result = await axios.post("http://localhost:8080/login", {
            user_name: username,
            password: encryptedPassword,
        });

        try {
            const id = login_result.data.data[0].user_id;
            if (id) {
                if (login_result.data.status) {
                    if (login_result.data.auth) {
                        secureLocalStorage.setItem('Token', login_result.data.auth);
                        login(username, id);
                        setSLoading(false);
                        navigate('/Home');
                    }
                } else {
                    toast.error(login_result.data.message, { className: 'error-toast' });
                }
            }
        } catch (error) {
            toast.error(login_result.data.message, { className: 'error-toast' });
        } finally {
        }
    }

    function generateRandomNumber(min, max) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }

    const handleChange = (index, value) => {
        const newOtpParts = [...verifyotp];
        newOtpParts[index] = value;
        setVerifyOtp(newOtpParts);

        if (index < inputRefs.current.length - 1) {
            inputRefs.current[index + 1].focus();
        }

    };

    return (
        <div class="container-fluid">
            <ToastContainer
                position="bottom-center"
                autoClose={3000}
                transition={Zoom}
            />
            <div class="d-flex flex-column min-vh-100 justify-content-center align-items-center">
                <div class="border border-secondary rounded bg w-25">
                    <h4 class="text-center"><small>
                        <label id="lblforgotpassword" style={{ display: 'block' }}>Forgot Password</label>
                        <label id="lblchangepassword" style={{ display: 'none' }}>Change Password</label>
                    </small></h4>
                    <hr />

                    <div id="panelforgotpassword" style={{ display: 'block' }}>
                        <form id="formforgotpassword" onSubmit={handleSubmit}>
                            <div class="p-1"></div>
                            <div class="row">
                                <div class="col-sm-1">
                                </div>
                                <div class="col-sm-10">
                                    <div className="text-input">
                                        <label className={usernameFocused || username !== "" ? "label-focus" : ""} htmlFor="lblusername">Username</label>
                                        <input
                                            id="txtusername"
                                            type="text"
                                            className={`form-control ${usernameFocused || username !== "" ? "input-focus" : ""}`}
                                            pattern=".{5,64}"
                                            required
                                            title="5 to 64 characters minimum"
                                            onChange={(e) => setUsername(e.target.value)}
                                            onBlur={() => setUsernameFocused(false)}
                                            onFocus={() => setUsernameFocused(true)}
                                        />
                                        <span id="spusername"></span>
                                    </div>
                                </div>
                                <div class="col-sm-1">
                                </div>
                            </div>
                            <div class="p-1"></div>
                            <div class="p-1"></div>
                            <div class="p-1"></div>
                            <div class="p-1"></div>
                            <div class="row">
                                <div class="col-sm-1">
                                </div>
                                <div class="col-sm-10">
                                    <div class="d-grid">
                                        <button
                                            className="btn btn-primary btn-block"
                                            id="btnsubmit"
                                            type="submit"
                                        >
                                            {loading ? (
                                                // Display the spinner when loading is true
                                                <ScaleLoader height={10} width={5} color={"#fff"} loading={loading} />
                                            ) : (
                                                // Display "Log In" when loading is false
                                                "Submit"
                                            )}
                                        </button>
                                    </div>
                                </div>
                                <div class="col-sm-1">
                                </div>
                            </div>
                            <br />
                        </form>
                    </div>

                    <div id="panelverifyotp" style={{ display: 'none' }}>
                        <form id="formforgotpassword" onSubmit={handleverify}>
                            <div class="p-1"></div>

                            <div class="row">
                                <div class="col-sm-1">
                                </div>
                                <div class="col-sm-10 input-group" >
                                    {verifyotp.map((part, index) => (
                                        <input
                                            key={index}
                                            type="text"
                                            className="form-control ms-2"
                                            pattern="[0-9]*"
                                            maxLength={1}
                                            style={{ width: 'calc(80% / 12)', marginRight: '5px' }}
                                            value={part}
                                            onChange={(e) => handleChange(index, e.target.value.replace(/\D/, ''))}
                                            ref={(input) => {
                                                inputRefs.current[index] = input;
                                            }}
                                            required
                                        />
                                    ))}
                                </div>
                                <div class="col-sm-1">
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-sm-1">
                                </div>
                                <div class="col-sm-10">
                                    <label for="lblsentmail" id="lblsendmail">Mail Sent..!Check Your email Address <b className="bg-warning">{user_mail}</b>.</label>
                                </div>
                                <div class="col-sm-1">
                                </div>
                            </div>
                            <div class="p-1"></div>
                            <div class="row">
                                <div class="col-sm-1">
                                </div>
                                <div class="col-sm-10">
                                    <div class="d-grid">
                                        <button
                                            className="btn btn-primary btn-block"
                                            id="btnverify"
                                            type="submit"
                                        >
                                            {vloading ? (
                                                // Display the spinner when loading is true
                                                <ScaleLoader height={10} width={5} color={"#fff"} loading={vloading} />
                                            ) : (
                                                // Display "Log In" when loading is false
                                                "Verify"
                                            )}

                                        </button>
                                    </div>
                                </div>
                                <div class="col-sm-1">
                                </div>
                            </div>
                            <br />
                        </form>
                    </div>

                    <div id="panelchangepassword" style={{ display: 'none' }} onSubmit={handleSaveNewPassword}>
                        <form>
                            <div class="row">
                                <div class="col-sm-1"></div>
                                <div class="col-sm-10">
                                    <div class="form-group form-control-sm">
                                        <div className="text-input">
                                            <label className={passwordFocused || newpassword !== "" ? "label-focus" : ""} htmlFor="lblnewpassword">New Password</label>
                                            <input
                                                id="txtnewpassword"
                                                type="password"
                                                className={`form-control form-control-sm ${passwordFocused || newpassword !== "" ? "input-focus" : ""}`}
                                                autoComplete="off"
                                                onChange={(e) => setNewPassword(e.target.value)}
                                                onBlur={() => setPasswordFocused(false)}
                                                onFocus={() => setPasswordFocused(true)}
                                                value={newpassword}
                                                required
                                                pattern=".{5,64}"
                                                title="Must contain at least 5 to 64 characters"
                                            />
                                            <span id="Spannewpass"></span>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-sm-1"></div>
                            </div>
                            <div class="row">
                                <div class="col-sm-1"></div>
                                <div class="col-sm-10">
                                    <div class="form-group form-control-sm">
                                        <div className="text-input">
                                            <label className={confirmpasswordFocused || confirmpassword !== "" ? "label-focus" : ""} htmlFor="lblconfirmpassword">Confirm Password</label>
                                            <input
                                                id="txtconfirmuserpassword"
                                                type="password"
                                                className={`form-control form-control-sm ${confirmpasswordFocused || confirmpassword !== "" ? "input-focus" : ""}`}
                                                autoComplete="off"
                                                onChange={(e) => setConfirmPassword(e.target.value)}
                                                onBlur={() => setConfirmPasswordFocused(false)}
                                                onFocus={() => setConfirmPasswordFocused(true)}
                                                value={confirmpassword}
                                                required
                                                minLength={5}
                                                maxLength={64}
                                                pattern={newpassword ? `^${newpassword}$` : ''}
                                                title="Passwords do not match and Must contain at least 5 characters"
                                            />
                                            <span id="Spanpass"></span>
                                        </div>
                                    </div>
                                </div>
                                <div class="col-sm-1"></div>
                            </div>
                            <div class="row">
                                <div class="col-sm-2"></div>
                                <div class="col-sm-8">
                                    <div id="dvuserpasswordmsg" style={{ display: 'none' }} align="center">
                                        <span id="Spancpmsg" ></span>
                                    </div>
                                </div>
                                <div class="col-sm-2"></div>
                            </div>
                            <br />
                            <div class="row">
                                <div class="col-sm-1"></div>

                                <div class="col-sm-10">
                                    <div class="form-group form-control-sm d-grid">
                                        <button
                                            id="btn_new_password"
                                            className="btn btn-primary btn-sm btn-block"
                                            type="submit"
                                        >

                                            {sloading ? (
                                                // Display the spinner when loading is true
                                                <ScaleLoader height={10} width={5} color={"#fff"} loading={sloading} />
                                            ) : (
                                                // Display "Log In" when loading is false
                                                "Save"
                                            )}
                                        </button>
                                    </div>
                                </div>
                                <div class="col-sm-1"></div>
                            </div>
                        </form>
                    </div>

                </div>
            </div>
        </div>
    );
}

export default Forgotpassword;
