          setEditPort(display_result.data.data[0].port)
          let decryptedToken;
          if (display_result.data.data[0].token) {
            decryptedToken = await Decrypt(display_result.data.data[0].token);
          } else {
            decryptedToken = display_result.data.data[0].token;
          }
          setEditToken(decryptedToken)
          const decryptedUsername = await Decrypt(display_result.data.data[0].username);
          setEditUsername(decryptedUsername)
