import React, { useState, useEffect } from "react";
import Header from "./Header.js";
import Slidermenu from "./Slidermenu.js";
import axiosInstance from './axios-interceptor.js';
import CryptoJS from 'crypto-js';
import TableLoader from '../Component/TableLoader.js'
import { ScaleLoader, BounceLoader, PuffLoader } from "react-spinners";
import { MultiSelect } from "../Component/MultiSelect.js";
import { jwtDecode } from 'jwt-decode';
import { useNavigate } from 'react-router-dom';
import secureLocalStorage from 'react-secure-storage';
import withNetworkStatus from '../Component/NetworkStatusHOC.js';
import AlertModel from '../Component/AlertModel.js';
import DataTableModel from '../Component/DataTable.js';
import { } from '../CSS/custom.scss';
import { useAuth } from '../Context/AuthContext.js';
import { Zoom, ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Datamigration_metadata() {
  const { usertype, username } = useAuth();
  const [formValid, setFormValid] = useState(false);
  const [editIndex, setEditIndex] = useState(null);
  const [joinColumn, setJoinColumn] = useState(null);
  const [incrColumn, setIncrColumn] = useState(null);
  const [TableFlowDatas, setTableFlowDatas] = useState([]);// Your grid data
  const [sourcedatabase, setSourceDatabase] = useState([]);
  const [targetdatabase, setTargetDatabase] = useState([]);
  const [selectedTables, setSelectedTables] = useState([]);
  const [generateMetadata, setGenerateMetadata] = useState([]);
  const [showGenTable, setShowGenTable] = useState(false);
  const [showTable, setShowTable] = useState(false);
  const [loading, setLoading] = useState(true);
  const [smVisible, setSmVisible] = useState(false);
  const [issmLoading, setSmLoading] = useState(false);
  const [issdLoading, setSdLoading] = useState(false);
  const [isssLoading, setSsLoading] = useState(false);
  const [isadLoading, setAdLoading] = useState(false);
  const [isesvLoading, setisesvLoading] = useState(false);
  const [isrmLoading, setisrmLoading] = useState(false);
  const [sourceschema, setSourceSchema] = useState([]);
  const [sourcetable, setSourceTable] = useState([]);
  const [threshold, setThreshold] = useState('');
  const [targetSchema, setTargetSchema] = useState('');
  const [selectedDatabase, setSelectedDatabase] = useState('');
  const [selectedSchema, setSelectedSchema] = useState('');
  const [Table_Id, setTableId] = useState(null);
  const [Source_Id, setSourceId] = useState(null);
  const [showModal_tableflowdeleterecord, setShowModal_tableflowdeleterecord] = useState(false);
  const [showModal_Popsavemetadata, setShowModal_Popsavemetadata] = useState(false);
  const [tableName, setTableName] = useState('');
  const [editSourceSchemaname, setEditSourceSchemaname] = useState('');
  const [editSourceTblname, setEditSourceTblname] = useState('');
  const [editTargetSchemaname, setEditTargetSchemaname] = useState('');
  const [editTargetTblname, setEditTargetTblname] = useState('');
  const [editOperationType, setEditOperationType] = useState('');
  const [editLoadType, setEditLoadType] = useState('');
  const [editJoinColumn, setEditJoinColumn] = useState('');
  const [editIncrColumn, setEditIncrColumn] = useState('');
  const [isexistsTables, setExistsTables] = useState('');
  const [islblExists, setIsLblExists] = useState(false);
  const [thresholdFocused, setThresholdFocused] = useState(false);
  const [sourcedatabaseFocused, setSourceDatabaseFocused] = useState(false);
  const [sourceschemaFocused, setSourceSchemaFocused] = useState(false);
  const [targetdatabaseFocused, setTargetDatabaseFocused] = useState(false);

  const [currentPage, setCurrentPage] = useState(1);
  const [totalrecords, setTotalRecords] = useState(0);
  const itemsPerPage = 10;
  const totalPages = Math.ceil(totalrecords / itemsPerPage);

  const navigate = useNavigate()
  const { id } = useAuth();

  const [showclear, setShowClear] = useState(false);

  useEffect(() => {
    if (selectedDatabase.length !== 0 || selectedSchema.length !== 0 || selectedTables.length !== 0 || targetSchema.length !== 0 || threshold !=='') {
      setShowClear(true);
    } else {
      setShowClear(false);
    }
  }, [selectedDatabase,selectedSchema,selectedTables,targetSchema,threshold]);

  const CheckToken = async () => {
    if (!secureLocalStorage.getItem('Token')) {
      return false;
    } else {
      const decoded = jwtDecode(secureLocalStorage.getItem('Token'));
      const currentTimestamp = Math.floor(Date.now() / 1000);

      if (decoded.exp < currentTimestamp) {
        return false;
      } else {
        const display_result = await axiosInstance.get("get_token", {
          params: {
            user_id: id
          }
        });
        if (display_result.data.status) {
          const newToken = secureLocalStorage.getItem('Token');
          const storedtoken = display_result.data.data[0].token;
          if (newToken !== storedtoken) {
            return false;
          } else {
            return true;
          }
        }
      }
    }
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    setGenerateMetadata([]);
    setShowGenTable(false);
    if (formValid) {
      await GenerateDataTable(e);
    }
  };

  useEffect(() => {
    if (sourcedatabase && sourceschema && sourcetable && targetdatabase && threshold) {
      setFormValid(true);
    } else {
      setFormValid(false);
    }
  }, [sourcedatabase, sourceschema, sourcetable, targetdatabase, threshold]);

  async function handleSave() {
    try {
      let chk_token = await CheckToken();
      if (chk_token) {
        setisesvLoading(true);
        const update_result = await axiosInstance.put("put_table_metadata",
          {
            source_schema_name: editSourceSchemaname,
            source_table_name: editSourceTblname,
            target_schema_name: editTargetSchemaname,
            target_table_name: editTargetTblname,
            operation: editOperationType,
            extract_load_type: editLoadType,
            join_column: editJoinColumn,
            incr_column_name: editIncrColumn,
            source_id: Source_Id,
            table_id: Table_Id

          });
        if (update_result.data.status) {
          toast.success(update_result.data.message, {className: 'success-toast'});
          Load();
        } else {
          toast.error(update_result.data.message, {className: 'error-toast'});
        }
        setEditIndex(null);
      } else {
        navigate('/')
      }
    }
    catch (error) {
      toast.error("Connection Failed", {className: 'error-toast'});
    }
    setisesvLoading(false);
  }

  async function handleSaveClick() {
    let chk_token = await CheckToken();
    if (chk_token) {
      setSmLoading(true);
      const replacer = (key, value) => {
        if (key === 'table_id' || key === 'source_table_count' || key === 'join_columns' || key === 'incr_columns') {
          return undefined; // Exclude the specified keys
        }
        return value; // Include all other keys
      };

      const jsonString = JSON.stringify(generateMetadata, replacer, 2); // 2 is for indentation
      const jsonArray = JSON.parse(jsonString);
      const arrayOfArrays = jsonArray.map(item => Object.values(item));
      const multiple_metadata = arrayOfArrays.map(item =>
        item.map(value =>
          typeof value === 'string' && value.includes('(') && value.includes(')')
            ? value.replace(/ \([^)]*\)/g, '')
            : value === 'None'
              ? null
              : value
        )
      );

      try {
        const insert_result = await axiosInstance.post("Add_table_metadata",
          multiple_metadata
        );

        if (insert_result.data.status) {
          toast.success(insert_result.data.message, {className: 'success-toast'});
          Load();
          handleResetClick();
        } else {
          toast.error(insert_result.data.message, {className: 'error-toast'});
        }

      }
      catch (error) {
        toast.error("Add Metadata Failed", {className: 'error-toast'});
      }
      setSmLoading(false);
    } else {
      navigate('/')
    }
  }

  const handleCancel = () => {
    // Reset the edit state without saving changes
    setEditIndex(null);
  };

  const handleEdit = (index) => {
    setEditIndex(index);
    setTableId(TableFlowDatas[index].table_id);
    setSourceId(TableFlowDatas[index].source_id);
    setEditSourceSchemaname(TableFlowDatas[index]['Source Schema Name']);
    setEditSourceTblname(TableFlowDatas[index]['Source Table Name']);
    setEditTargetSchemaname(TableFlowDatas[index]['Target Schema Name']);
    setEditTargetTblname(TableFlowDatas[index]['Target Table Name']);
    setEditOperationType(TableFlowDatas[index]['Operation']);
    setEditLoadType(TableFlowDatas[index]['Extract Load Type']);
    setEditJoinColumn(TableFlowDatas[index]['Join Column']);
    setEditIncrColumn(TableFlowDatas[index]['INCR Column Name']);
  }

  const openModal_tableflowdeleterecord = (table_id, table_name) => {
    setShowModal_tableflowdeleterecord(true);
    setTableId(table_id);
    setTableName(table_name);
  };

  const closeModal_tableflowdeleterecord = () => {
    setShowModal_tableflowdeleterecord(false);
  };
  const closeModal_Popsavemetadata = () => {
    setShowModal_Popsavemetadata(false);
  };

  async function get_incr_column(tableName) {
    let chk_token = await CheckToken();
    if (chk_token) {
      const res = await ConnectionString(selectedDatabase);
      const username = await Decrypt(res.data.data[0].username);
      const password = await Decrypt(res.data.data[0].password);
      const hostname = await Decrypt(res.data.data[0].hostname);

      const table_column_result = await axiosInstance.get("get_mssql_column_name", {
        params: {
          db_name: selectedSchema,
          table_name: tableName,
          user: username,
          password: password,
          host: hostname
        }
      });
      const tbl_column = table_column_result.data.data.recordsets[0];
      // Find the column named "updated_datetime"
      const updatedDatetimeItem = tbl_column.find((item) => item.col_name.includes('updated_datetime'));

      // If "updated_datetime" column is found, use it; otherwise, find any "datetime" column
      const selectedColumn = updatedDatetimeItem
        ? `${updatedDatetimeItem.col_name} (${updatedDatetimeItem.data_type})`
        : tbl_column.find((item) => item.col_name.includes('datetime'))
          ? `${tbl_column.find((item) => item.col_name.includes('datetime')).col_name} (${tbl_column.find((item) => item.col_name.includes('datetime')).data_type})`
          : null;

      return [selectedColumn, tbl_column];
    } else {
      navigate('/')
    }
  }

  async function get_pk_column(tableName) {
    let chk_token = await CheckToken();
    if (chk_token) {
      const res = await ConnectionString(selectedDatabase);
      const username = await Decrypt(res.data.data[0].username);
      const password = await Decrypt(res.data.data[0].password);
      const hostname = await Decrypt(res.data.data[0].hostname);
      const table_pk_result = await axiosInstance.get("get_mssql_source_pk_column", {
        params: {
          db_name: selectedSchema,
          table_name: tableName,
          user: username,
          password: password,
          host: hostname
        }
      });
      const pk_status = table_pk_result.data.data.recordset[0].STATUS;
      const pk = table_pk_result.data.data.recordset[0].PRIMARY_KEYS;
      return [pk_status, pk];
    } else {
      navigate('/')
    }
  }

  useEffect(() => {
    (async () => {
      let chk_token = await CheckToken();
      if (chk_token) {
        await Promise.all([SourceDatabase(), TargetDatabase(), Load()]);
        setLoading(false);
        setShowTable(true);
      } else {
        navigate('/')
      }
    })();
  }, [currentPage, generateMetadata]);

  async function Load() {
    let chk_token = await CheckToken();
    if (chk_token) {
      await counttotalrecords();
      const display_result = await axiosInstance.get("get_table_metadata", {
        params: {
          current: (currentPage - 1) * itemsPerPage,
          pagesize: itemsPerPage
        }
      });
      if (display_result.data.status) {
        setTableFlowDatas(display_result.data.data);
      }
    } else {
      navigate('/')
    }
  }

  const handleInputChange = async (index, fieldValues) => {
    let chk_token = await CheckToken();
    if (chk_token) {
      setGenerateMetadata((prevMetadata) => {
        const updatedMetadata = [...prevMetadata];
        Object.entries(fieldValues).forEach(async ([field, value]) => {
          if (joinColumn === null && incrColumn === null) {
            const joinColumnName = generateMetadata[index]["join_column"];
            const incrColumnName = generateMetadata[index]["incr_column_name"];
            setJoinColumn({ id: index, name: joinColumnName });
            setIncrColumn({ id: index, name: incrColumnName });
          } else if ((joinColumn?.id !== index || incrColumn?.id !== index) && (!joinColumn || !incrColumn)) {
            const joinColumnName = generateMetadata[index]["join_column"];
            const incrColumnName = generateMetadata[index]["incr_column_name"];
            setJoinColumn({ id: index, name: joinColumnName });
            setIncrColumn({ id: index, name: incrColumnName });
          }
  
          if (field === "extract_load_type" && value === "FULL") {
            updatedMetadata[index][field] = value;
            updatedMetadata[index + 1][field] = value;
            updatedMetadata[index].join_column = "None";
            updatedMetadata[index].incr_column_name = "None";
            updatedMetadata[index + 1].join_column = "None";
            updatedMetadata[index + 1].incr_column_name = "None";
          } else if (field === "extract_load_type" && value === "INCR") {
            updatedMetadata[index][field] = value;
            updatedMetadata[index + 1][field] = value;
            updatedMetadata[index].join_column = joinColumn?.name || "None";
            updatedMetadata[index].incr_column_name = incrColumn?.name || "None";
            updatedMetadata[index + 1].join_column = joinColumn?.name || "None";
            updatedMetadata[index + 1].incr_column_name = incrColumn?.name || "None";
          } else {
            updatedMetadata[index][field] = value;
          }
        });
        return updatedMetadata;
      });
    } else {
      navigate('/');
    }
  };
  

  const handlePageChange = (newPage) => {
    if (newPage >= 1 && newPage <= totalPages) {
      setCurrentPage(newPage);
    }
  }

  async function handleResetClick() {
    setShowGenTable(false);
    setGenerateMetadata([]);
    setSmVisible(false);
    setIsLblExists(false);
    setSelectedDatabase('');
    setSelectedSchema('');
    setSourceSchema([]);
    setSelectedTables([]);
    setSourceTable([]);
    setThreshold('');
    setTargetSchema('');
  }


  const handleTableSelection = (selectedOption) => {
    setSelectedTables(selectedOption);
  };

  async function GenerateDataTable() {
    let chk_token = await CheckToken();
    if (chk_token) {
      setAdLoading(true);
      let query = '';
      let existstbls = '';
      let c = 1;
      const selectedTableNames = selectedTables.map((option) => option.value);
      selectedTableNames.forEach((tableName) => {
        query += `(SELECT COUNT(*) FROM ${tableName}) AS ${tableName}, `;
      });

      query = query.slice(0, -2);
      query = "SELECT " + query + "";

      const res = await ConnectionString(selectedDatabase);
      const username = await Decrypt(res.data.data[0].username);
      const password = await Decrypt(res.data.data[0].password);
      const hostname = await Decrypt(res.data.data[0].hostname);
      const table_count_result = await axiosInstance.get("get_mssql_count_table_data", {
        params: {
          db_name: selectedSchema,
          query: query,
          user: username,
          password: password,
          host: hostname
        }
      });
      const all_tbl_count = table_count_result.data.data.recordsets[0]
      for (const tableName of selectedTableNames) {
        let mSource_Id = "";
        let pk = "";
        let bronze_metadata = "b";
        let metadatacount = 0;
        let table_count = all_tbl_count[0][tableName];
        let load_type = "";
        let INCR_column = "";
        const selectedColumn = await get_incr_column(tableName);
        const available_pk = await get_pk_column(tableName);

        for (let i = 0; i < 2; i++) {
          if (bronze_metadata !== "") {
            const metadata_exists_result = await axiosInstance.get("get_metadata_exists", {
              params: {
                source_schema_name: selectedSchema,
                source_table_name: tableName,
                target_schema_name: "dev.bronze",
                target_table_name: tableName.toLowerCase()
              }
            });

            metadatacount = metadata_exists_result.data.data[0].record_count;

            if (metadatacount == 0) {
              bronze_metadata = "";
              if (table_count >= threshold) {
                if (available_pk[0] == "true") {
                  pk = available_pk[1];
                  let primaryKeyArray = pk.split(',').map((item) => item.trim());
                  pk = primaryKeyArray.length > 0 ? primaryKeyArray[0] : null;
                  load_type = "INCR";
                  INCR_column = selectedColumn[0];
                } else {
                  load_type = "FULL";
                  pk = "None";
                  INCR_column = "None";
                }
              } else {
                load_type = "FULL";
                pk = "None";
                INCR_column = "None";
              }

              const database_id_result = await axiosInstance.get("get_connection_id", {
                params: {
                  database_name: selectedDatabase
                }
              });

              mSource_Id = database_id_result.data.data[0].database_id;
              const bronzeItem = {
                "source_id": mSource_Id,
                "table_id": c,
                "source_schema_name": selectedSchema,
                "source_table_name": tableName,
                "target_schema_name": "dev.bronze",
                "target_table_name": tableName.toLowerCase(),
                "operation": "Extract",
                "source_table_count": table_count,
                "extract_load_type": load_type,
                "join_column": pk,
                "incr_column_name": INCR_column,
                "join_columns": selectedColumn[1],
                "incr_columns": selectedColumn[1]
              };
              setGenerateMetadata((prevList) => [...prevList, bronzeItem]);
              c++;
            } else {
              bronze_metadata = "";
              existstbls += tableName + ",";
            }
          } else {
            if (metadatacount == 0) {
              const database_id_result = await axiosInstance.get("get_connection_id", {
                params: {
                  database_name: targetSchema
                }
              });
              mSource_Id = database_id_result.data.data[0].database_id;

              const silverItem = {
                "source_id": mSource_Id,
                "table_id": c,
                "source_schema_name": "dev.bronze",
                "source_table_name": tableName,
                "target_schema_name": "dev.silver",
                "target_table_name": tableName.toLowerCase(),
                "operation": "Load",
                "source_table_count": table_count,
                "extract_load_type": load_type,
                "join_column": pk,
                "incr_column_name": INCR_column,
                "join_columns": selectedColumn[1],
                "incr_columns": selectedColumn[1]
              };
              setGenerateMetadata((prevList) => [...prevList, silverItem]);
              c++;
            }
          }

        }
      }
      if (existstbls) {
        setShowGenTable(false);
        setExistsTables(existstbls);
        setIsLblExists(true);
        setSmVisible(false);

      } else {
        setShowGenTable(true);
      }
      setAdLoading(false);
      setSmVisible(true);
    } else {
      navigate('/')
    }
  }


  async function SourceDatabase() {
    const display_result = await axiosInstance.get("get_database_name_migration", {
    });
    if (display_result.data.status) {
      setSourceDatabase(display_result.data.data);
    }
  }

  async function TargetDatabase() {
    const display_result = await axiosInstance.get("get_target_database_name", {
    });
    if (display_result.data.status) {
      setTargetDatabase(display_result.data.data);
    }
  }

  async function ConnectionString(database_name) {
    const display_result = await axiosInstance.get("get_connection_string", {
      params: {
        user_name: username,
        database_name: database_name
      }
    });
    return display_result
  }

  async function counttotalrecords() {
    const display_result = await axiosInstance.get("get_count_metadata", {
    });
    if (display_result.data.status) {
      setTotalRecords(display_result.data.data[0].table_id);
    }
  }

  async function Decrypt(encryptedPassword) {
    const key = CryptoJS.enc.Utf8.parse('GHYZXFZH2A4B6N6P7R9SPNMT');
    const decrypted = CryptoJS.TripleDES.decrypt(
      {
        ciphertext: CryptoJS.enc.Base64.parse(encryptedPassword),
      },
      key,
      {
        mode: CryptoJS.mode.ECB,
        padding: CryptoJS.pad.Pkcs7,
      }
    );
    return decrypted.toString(CryptoJS.enc.Utf8);
  }

  async function SourceTable(schema_name) {
    setShowGenTable(false);
    setGenerateMetadata([]);
    setSmVisible(false);
    setIsLblExists(false);
    setSelectedTables([]);
    
    let chk_token = await CheckToken();
    if (chk_token) {
      setSsLoading(true);
      setSelectedSchema(schema_name);
      const res = await ConnectionString(selectedDatabase);
      const username = await Decrypt(res.data.data[0].username);
      const password = await Decrypt(res.data.data[0].password);
      const hostname = await Decrypt(res.data.data[0].hostname);

      const display_result = await axiosInstance.get("get_mssql_table_name", {
        params: {
          schema_name: schema_name,
          user: username,
          password: password,
          host: hostname
        }
      });
      if (display_result.data.status) {
        setSourceTable(display_result.data.data.recordsets[0]);
      }
      setSsLoading(false);
    } else {
      navigate('/')
    }
  }

  async function SourceSchema(database_name) {
    setSelectedDatabase('');
    setSourceSchema([]);
    let chk_token = await CheckToken();
    if (chk_token) {
      setSelectedDatabase(database_name)
      setSdLoading(true);
      const res = await ConnectionString(database_name);
      const username = await Decrypt(res.data.data[0].username);
      const password = await Decrypt(res.data.data[0].password);
      const hostname = await Decrypt(res.data.data[0].hostname);

      const display_result = await axiosInstance.get("get_mssql_schema_name", {
        params: {
          user: username,
          password: password,
          host: hostname
        }
      });
      if (display_result.data.status) {
        setSourceSchema(display_result.data.data.recordsets[0]);
      }
      setSdLoading(false);
    } else {
      navigate('/')
    }
  }

  async function DeleteTableData(table_id) {
    let chk_token = await CheckToken();
    if (chk_token) {
      setisrmLoading(true);
      const delete_result = await axiosInstance.delete("delete_table_metadata", {
        data: { table_id: table_id }
      });
      if (delete_result.data.status) {
        toast.success(delete_result.data.message, {className: 'success-toast'});
        Load();
        closeModal_tableflowdeleterecord();
      } else {
        toast.error(delete_result.data.message, {className: 'error-toast'});
      }
      setisrmLoading(false);
    } else {
      navigate('/')
    }
  }

  return (
    <div>
      <Header></Header>
      <Slidermenu></Slidermenu>
      <div className="mt-2 p-2 ms-5">
        <div class="col-sm-12">
        <ToastContainer 
          position="bottom-center"
          autoClose={3000}
          transition={Zoom}
           />
          <form onSubmit={handleSubmit}>
            <div class="container-fluid border mb-2">
              {(usertype === process.env.REACT_APP_ADMIN || usertype === process.env.REACT_APP_EDITOR) && (
                <div>
                  <div class="row p-2">
                    <div class="row">
                      <h6 class="text-center display-7"><small>Add Table Metadata</small></h6>
                      <hr></hr>
                    </div>
                    <div class="col-sm-12">
                      <div class="input-group input-group-sm justify-content-md-start">
                        <div class="form-group form-control-sm" style={{ width: 200 }}>
                          <div className="text-input" style={{ position: 'relative' }}>
                            <label className={sourcedatabaseFocused || selectedDatabase !== "" ? "label-focus" : ""} htmlFor="ddsourcedatabase">
                              Source Database
                            </label>
                            <div className="select-wrapper">
                              <select
                                className={`form-control form-control-sm select-input ${sourcedatabaseFocused || selectedDatabase !== "" ? "input-focus" : ""}`}
                                id="ddsourcedatabase"
                                name="ddsourcedatabase"
                                onChange={(event) => SourceSchema(event.target.value)}
                                onBlur={() => setSourceDatabaseFocused(false)}
                                onFocus={() => setSourceDatabaseFocused(true)}
                                required
                                autoComplete="off"
                                value={selectedDatabase}
                              >
                                {(
                                  <option value="" hidden></option>
                                )}
                                {sourcedatabase.map((sourceDatabase) => (
                                  <option key={sourceDatabase.database_type} value={sourceDatabase.database_name}>
                                    {sourceDatabase.database_name}
                                  </option>
                                ))}
                              </select>
                            </div>
                          </div>
                        </div>

                        <div class="form-group form-control-sm" style={{ width: 200 }}>
                          <div className="text-input" style={{ position: 'relative' }}>
                            <label className={sourceschemaFocused || selectedSchema !== "" ? "label-focus" : ""} htmlFor="ddsourcedatabase">
                              Source Schema
                            </label>
                            <div className="select-wrapper">
                              {issdLoading && (
                                <div style={{ position: 'absolute', top: '50%', right: '8px', transform: 'translateY(-50%)' }}>
                                  <PuffLoader size={21} color={"#2E67F8"} loading={issdLoading} />
                                </div>
                              )}
                              <select
                                className={`form-control form-control-sm select-input ${sourceschemaFocused || selectedSchema !== "" ? "input-focus" : ""} `}
                                id="ddsourceschema"
                                name="ddsourceschema"
                                value={selectedSchema}
                                onChange={(event) => SourceTable(event.target.value)}
                                onBlur={() => setSourceSchemaFocused(false)}
                                onFocus={() => setSourceSchemaFocused(true)}
                                required
                                autoComplete="off"
                              >
                                {(
                                  <option value="" hidden></option>
                                )}
                                {sourceschema.map((sourceSchema) => (
                                  <option key={sourceSchema.database_id} value={sourceSchema.databaseName}>
                                    {sourceSchema.databaseName}
                                  </option>
                                ))}
                              </select>
                            </div>
                          </div>
                        </div>

                        <div className="form-group form-control-sm" style={{ width: 200 }}>
                          <div className="text-input">
                            <div className="select-wrapper">
                              <MultiSelect
                                id="ddsourcetablename"
                                name="ddsourcetablename"
                                options={sourcetable.map((sourceTable) => ({
                                  value: sourceTable.tableName,
                                  label: sourceTable.tableName,
                                }))}
                                placeholder="Source Table"
                                required
                                isLoading={isssLoading}
                                onChange={handleTableSelection}
                                value={selectedTables}
                              />
                            </div>
                          </div>
                        </div>

                        <div class="form-group form-control-sm" style={{ width: 200 }}>
                          <div className="text-input">
                            <label className={targetdatabaseFocused || targetSchema !== "" ? "label-focus" : ""} htmlFor="ddsourcedatabase">Target Solution</label>
                            <div className="select-wrapper">
                              <select
                                className={`form-control form-control-sm select-input ${targetdatabaseFocused || targetSchema !== "" ? "input-focus" : ""} `}
                                id="ddtargetsolution"
                                name="ddtargetsolution"
                                onChange={(e) => setTargetSchema(e.target.value)}
                                onBlur={() => setTargetDatabaseFocused(false)} onFocus={() => setTargetDatabaseFocused(true)}
                                required
                                autoComplete="off"
                                value={targetSchema}
                              >
                                {(
                                  <option value="" hidden></option>
                                )}
                                {targetdatabase.map((targetDatabase) => (
                                  <option key={targetDatabase.database_type} value={targetDatabase.database_name}>
                                    {targetDatabase.database_name}
                                  </option>
                                ))}
                              </select>
                            </div>
                          </div>
                        </div>

                        <div class="form-group form-control-sm">
                          <div className="text-input">
                            <label className={thresholdFocused || threshold !== "" ? "label-focus" : ""} htmlFor="txtthreshold">Threshold</label>

                            <input
                              type="text"
                              id="txtthreshold"
                              name="txtthreshold"
                              className={`form-control form-control-sm ${thresholdFocused || threshold !== "" ? "input-focus" : ""}`}
                              onChange={(e) => setThreshold(e.target.value)}
                              value={threshold}
                              onBlur={() => setThresholdFocused(false)}
                              onFocus={() => setThresholdFocused(true)}
                              required
                            />
                          </div>
                        </div>

                        <div class="form-group form-control-sm mt-1">
                          <button
                            id="btn_genmetadata"
                            className="btn btn-outline-primary btn-sm btn-block"
                            type="submit"
                          >
                            {isadLoading ? (
                              // Display the spinner when loading is true
                              <BounceLoader size={17} color={"#2E67F8"} loading={isadLoading} />
                            ) : (
                              "Add"
                            )}
                          </button>
                        </div>

                        <div class="form-group form-control-sm mt-1">
                          {showclear &&(
                          <button
                            id="btnreset"
                            className="btn btn-outline-primary btn-sm btn-block"
                            type="button"
                            onClick={handleResetClick}
                          >
                            Clear
                          </button>
                          )}
                        </div>
                      </div>
                    </div>
                  </div>

                </div>
              )}
            </div>
            <div class="container-fluid border">
              <div class="row">
                <h6 class="text-center display-7"><small>Data ingestion metadata summary</small></h6>
              </div>

              {showGenTable && (
                <div>
                  <div className="row">
                    <div class="input-group input-group-sm justify-content-md-start">
                      <div class="form-group form-control-sm m-auto me-0 mb-2">
                        <button
                          id="btnsave"
                          className="btn btn-outline-success btn-sm btn-block m-auto me-0"
                          type="button"
                          onClick={handleSaveClick}
                          hidden={!smVisible}
                        >
                          {issmLoading ? (
                            // Display the spinner when loading is true
                            <ScaleLoader height={10} width={5} color={"#198754"} loading={issmLoading} />
                          ) : (
                            "Save Metadata"
                          )}
                        </button>
                      </div>
                    </div>
                  </div>
                  <div id="generatetable" class="row">
                    <div className="table-responsive">
                      <table className="table table-bordered table-hover table-striped table-responsive-sm">
                        <thead className="thead-dark">
                          <tr>
                            <th className="align-top">#</th>
                            <th>Source Schema Name</th>
                            <th>Source Table Name</th>
                            <th>Target Schema Name</th>
                            <th>Target Table Name</th>
                            <th className="align-top">Operation</th>
                            <th>Source Table Count</th>
                            <th>Extract Load Type</th>
                            <th className="align-top">Join Column</th>
                            <th className="align-top">INCR Column Name</th>
                          </tr>
                        </thead>
                        {generateMetadata.map(function fn(tbl_gen_data, index) {
                          return (
                            <tbody key={tbl_gen_data.table_id}>
                              <tr>
                                <td>
                                  {tbl_gen_data.table_id}
                                </td>
                                <td>
                                  <input
                                    type="text"
                                    className="form-control"
                                    value={tbl_gen_data.source_schema_name}
                                    onChange={(e) => handleInputChange(index, { source_schema_name: e.target.value })}
                                  />
                                </td>
                                <td>
                                  <input
                                    type="text"
                                    className="form-control"
                                    value={tbl_gen_data.source_table_name}
                                    onChange={(e) => handleInputChange(index, { source_table_name: e.target.value })}
                                  />
                                </td>

                                <td>
                                  <input
                                    type="text"
                                    className="form-control"
                                    value={tbl_gen_data.target_schema_name}
                                    onChange={(e) => handleInputChange(index, { target_schema_name: e.target.value })}
                                  />
                                </td>
                                <td>
                                  <input
                                    type="text"
                                    className="form-control"
                                    value={tbl_gen_data.target_table_name}
                                    onChange={(e) => handleInputChange(index, { target_table_name: e.target.value })}
                                  />
                                </td>
                                <td>
                                  <select
                                    className="form-select form-select-sm"
                                    value={tbl_gen_data.operation}
                                    onChange={(e) => handleInputChange(index, { operation: e.target.value })}
                                  >
                                    <option value="" disabled>Select Operation</option>
                                    <option value="Load">Load</option>
                                    <option value="Extract">Extract</option>
                                  </select>
                                </td>
                                <td>
                                  <input
                                    type="text"
                                    className="form-control"
                                    value={tbl_gen_data.source_table_count}
                                  />
                                </td>
                                <td>
                                  <select
                                    className="form-select form-select-sm"
                                    value={tbl_gen_data.extract_load_type}
                                    onChange={(e) => handleInputChange(index, {
                                      extract_load_type: e.target.value
                                    })}
                                    disabled={index % 2 === 1}
                                  >
                                    <option value="" disabled>Select LoadType</option>
                                    <option value="FULL">Full</option>
                                    <option value="INCR">Incr</option>
                                  </select>
                                </td>
                                <td>
                                  <select
                                    className="form-select form-select-sm"
                                    onChange={(e) => handleInputChange(index, { join_column: e.target.value })}
                                    autoComplete="off"
                                    value={generateMetadata[index].join_column}
                                    disabled={tbl_gen_data.join_column == "None"}
                                  >
                                    {(
                                      <option value="" disabled selected>
                                        Join Column
                                      </option>
                                    )}
                                    {(
                                      <option value="None">
                                        None
                                      </option>
                                    )}
                                    {tbl_gen_data.join_columns.map((joinColumn) => (
                                      <option key={joinColumn.col_name} value={joinColumn.col_name + " (" + joinColumn.data_type + ")"}>
                                        {joinColumn.col_name + " (" + joinColumn.data_type + ")"}
                                      </option>
                                    ))}
                                  </select>
                                </td>
                                <td><select
                                  className="form-select form-select-sm"
                                  autoComplete="off"
                                  onChange={(e) => handleInputChange(index, { incr_column_name: e.target.value })}
                                  value={generateMetadata[index].incr_column_name}
                                  disabled={tbl_gen_data.incr_column_name == "None"}
                                >
                                  {(
                                    <option value="" disabled selected>
                                      Incr Column Name
                                    </option>
                                  )}
                                  {(
                                    <option value={null}>
                                      None
                                    </option>
                                  )}
                                  {tbl_gen_data.join_columns.map((incrColumn) => (
                                    <option key={incrColumn.col_name} value={incrColumn.col_name + " (" + incrColumn.data_type + ")"}>
                                      {incrColumn.col_name + " (" + incrColumn.data_type + ")"}
                                    </option>
                                  ))}
                                </select>
                                </td>
                              </tr>
                            </tbody>
                          );
                        })}
                      </table>
                    </div>
                  </div>
                </div>
              )}

              <div class="row text-center">
                <label id="lblexiststables" Class="text-danger justify-content-center" hidden={!islblExists}>
                  {`Already ${isexistsTables.trim().slice(0, -1)} table information is stored in table metadata`}
                </label>
              </div>
              <div class="row">
                <div id="addmetadata" cssclass="collapse">
                  <div id="tbldiv" className="table-responsive table-responsive-sm">
                    {/* Your content goes here */}
                  </div>
                  <div class="col-sm-12">
                    <div class="row">
                      <div class="input-group input-control-sm">
                        <div class="col-sm-3"></div>
                        <div class="col-sm-3"></div>
                        <div class="col-sm-3"></div>
                        <div class="col-sm-2">

                        </div>
                      </div>

                    </div>
                  </div>
                </div>
              </div>

              <br />
              {(usertype === process.env.REACT_APP_ADMIN) && (
                <div>
                  {loading ? (
                    // Display the spinner when loading is true
                    <TableLoader loading={loading} />
                  ) : (
                    <DataTableModel
                      showTable={showTable}
                      headings={TableFlowDatas.length > 0 ?[
                        ...Object.keys(TableFlowDatas[0]).slice(3).map((key, index) => (
                          <th key={index}>{key}</th>
                        )),
                        <th key="Actions">Actions</th>
                      ]:[]}
                      editActions={true}
                      items={TableFlowDatas}
                      removeItems={["table_id", "Source Table Name"]}
                      removeActions={openModal_tableflowdeleterecord}
                      handleCancel={handleCancel}
                      handleSave={handleSave}
                      handleEdit={handleEdit}
                      editableIndex={editIndex}
                      editedItems={[editSourceSchemaname, editSourceTblname, editTargetSchemaname, editTargetTblname, editOperationType, editLoadType, editJoinColumn, editIncrColumn]}
                      setEditedItems={[setEditSourceSchemaname, setEditSourceTblname, setEditTargetSchemaname, setEditTargetTblname, setEditOperationType, setEditLoadType, setEditJoinColumn, setEditIncrColumn]}
                      currentPage={currentPage}
                      totalPages={totalPages}
                      prevPage={() => handlePageChange(currentPage - 1)}
                      currPage={handlePageChange}
                      nxtPage={() => handlePageChange(currentPage + 1)}
                      isesvLoading={isesvLoading}
                    />
                  )}
                </div>
              )}

              {(usertype === process.env.REACT_APP_EDITOR) && (
                <div>
                  {loading ? (
                    // Display the spinner when loading is true
                    <TableLoader loading={loading} />
                  ) : (
                    <DataTableModel
                      showTable={showTable}
                      headings={TableFlowDatas.length > 0 ?[
                        ...Object.keys(TableFlowDatas[0]).slice(3).map((key, index) => (
                          <th key={index}>{key}</th>
                        )),
                        <th key="Actions">Actions</th>
                      ]:[]}
                      editActions={true}
                      items={TableFlowDatas}
                      handleCancel={handleCancel}
                      handleSave={handleSave}
                      handleEdit={handleEdit}
                      editableIndex={editIndex}
                      editedItems={[editSourceSchemaname, editSourceTblname, editTargetSchemaname, editTargetTblname, editOperationType, editLoadType, editJoinColumn, editIncrColumn]}
                      setEditedItems={[setEditSourceSchemaname, setEditSourceTblname, setEditTargetSchemaname, setEditTargetTblname, setEditOperationType, setEditLoadType, setEditJoinColumn, setEditIncrColumn]}
                      currentPage={currentPage}
                      totalPages={totalPages}
                      prevPage={() => handlePageChange(currentPage - 1)}
                      currPage={handlePageChange}
                      nxtPage={() => handlePageChange(currentPage + 1)}
                      isesvLoading={isesvLoading}
                    />
                  )}
                </div>
              )}

              {(usertype === process.env.REACT_APP_ANALYST) && (
                <div>
                  {loading ? (
                    // Display the spinner when loading is true
                    <TableLoader loading={loading} />
                  ) : (
                    <DataTableModel
                      showTable={showTable}
                      headings={TableFlowDatas.length > 0 ?[
                        ...Object.keys(TableFlowDatas[0]).slice(3).map((key, index) => (
                          <th key={index}>{key}</th>
                        )),
                      ]:[]}
                      items={TableFlowDatas}
                      editableIndex={editIndex}
                      editedItems={[editSourceSchemaname, editSourceTblname, editTargetSchemaname, editTargetTblname, editOperationType, editLoadType, editJoinColumn, editIncrColumn]}
                      setEditedItems={[setEditSourceSchemaname, setEditSourceTblname, setEditTargetSchemaname, setEditTargetTblname, setEditOperationType, setEditLoadType, setEditJoinColumn, setEditIncrColumn]}
                      currentPage={currentPage}
                      totalPages={totalPages}
                      prevPage={() => handlePageChange(currentPage - 1)}
                      currPage={handlePageChange}
                      nxtPage={() => handlePageChange(currentPage + 1)}
                      isesvLoading={isesvLoading}
                    />
                  )}
                </div>
              )}
            </div>
          </form>
        </div>

        <AlertModel
          modelTitle={"Remove Metadata Information"}
          lblName={<>remove <b>{tableName}</b> table?</>}
          showModal={showModal_tableflowdeleterecord}
          closeModal={closeModal_tableflowdeleterecord}
          isalLoading={isrmLoading}
          saveClick={() => DeleteTableData(Table_Id)}
          btnname={"Remove"}
          btncolor={"#DC3545"}
          btnclass={"btn btn-outline-danger btn-sm btn-block"}
        />

        <div id="Popsavemetadata" className={`modal fade ${showModal_Popsavemetadata ? 'show' : ''}`} role="dialog" style={{ display: showModal_Popsavemetadata ? 'block' : 'none' }}>
          <div class="modal-dialog">
            <div class="modal-content">

              <div class="modal-header">
                <h4 class="modal-title">Store Metadata</h4>
                <button type="button" class="btn-close" data-bs-dismiss="modal" data-dismiss="modal" onClick={closeModal_Popsavemetadata}></button>
              </div>

              <div class="modal-body ">
                <div class="container-sm bg-light border">

                  <div class="row">
                    <div class="col-sm-12" >
                      <label ID="lblmetamsg" />
                      <span id="spanmeta" ></span>
                    </div>
                  </div>
                  <br />
                </div>
              </div>
              <div class="modal-footer">
                <input type="button" value="Close" class="btn btn-outline-danger btn-sm btn-block" data-dismiss="modal" data-bs-dismiss="modal" onClick={closeModal_Popsavemetadata} />

              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default withNetworkStatus(Datamigration_metadata);