import { BounceLoader, ScaleLoader } from 'react-spinners';
import { Pagination, Button } from 'react-bootstrap';

const DataTable = ({
    showTable,
    headings,
    partitionHeaders,
    editActions,
    items,
    removeItems,
    removeActions,
    rollbackActions,
    handleCancel,
    handleSave,
    handleEdit,
    editableIndex,
    editedItems,
    setEditedItems,
    currentPage,
    totalPages,
    prevPage,
    currPage,
    nxtPage,
    isesvLoading,
    iscfLoading,
    iscsLoading,
    isgrdqLoading,
    setSelectedId,
    displayData,
    selectedId
}) => {
    const startPage = Math.max(1, currentPage - 2);
    const endPage = Math.min(totalPages, startPage + 4);
    const TableHeaderCell = ({ colSpan, text }) => {
        return (
            <th colSpan={colSpan} className="text-center bg-light">
                {text}
            </th>
        );
    };
    return (
        <div>
            {showTable && (
                <div class="row">
                    <div class="table-responsive">
                        <table className="table table-bordered table-hover">
                            <thead className="thead-dark">
                                <tr hidden={!partitionHeaders ? true : false}>
                                    <TableHeaderCell colSpan={5} text="Staging Layer" />
                                    <TableHeaderCell colSpan={5} text="Production Layer" />
                                    <TableHeaderCell colSpan={1} text="" />
                                </tr>
                                <tr style={{ verticalAlign: 'top' }}>
                                    {headings}
                                </tr>
                            </thead>
                            {items.length > 0 ? (
                                <tbody>
                                    {items.map((item, index) => (
                                        <tr key={index}>
                                            {headings.map((heading, cellIndex) => (
                                                <td key={cellIndex}>
                                                    {heading.props.children === "Actions" ? (
                                                        editableIndex === index ? (
                                                            <>
                                                                <div className="btn-group" role="group">
                                                                    <button
                                                                        type="button"
                                                                        className="btn btn-outline-success btn-sm btn-block me-2"
                                                                        onClick={handleSave}
                                                                    >
                                                                        {isesvLoading ? (
                                                                            // Display the spinner when loading is true
                                                                            <BounceLoader size={21} color={"#198754"} loading={isesvLoading} />
                                                                        ) : (
                                                                            <i class="bi bi-plus-lg"></i>
                                                                        )}
                                                                    </button>
                                                                    <button
                                                                        type="button"
                                                                        className="btn btn-outline-secondary btn-sm btn-block"
                                                                        onClick={handleCancel}
                                                                    >
                                                                        <i class="bi bi-x-lg"></i>
                                                                    </button>
                                                                </div>
                                                            </>
                                                        ) : (
                                                            <div className="btn-group" role="group">
                                                                <button
                                                                    type="button"
                                                                    className="btn btn-outline-primary btn-sm btn-block me-2"
                                                                    onClick={() => handleEdit(index)}
                                                                    hidden={!editActions ? true : false}
                                                                >
                                                                    <i class="bi bi-pencil"></i>
                                                                </button>
                                                                <button
                                                                    type="button"
                                                                    className="btn btn-outline-danger btn-sm btn-block"
                                                                    onClick={() => removeActions(item[removeItems[0]], item[removeItems[1]])}
                                                                    hidden={!removeActions ? true : false}
                                                                >
                                                                    <i class="bi bi-trash3"></i>
                                                                </button>
                                                                {!(index === 0 && currentPage === 1) && !(index === items.length - 1 && currentPage === totalPages) && (
                                                                    <button
                                                                        type="button"
                                                                        className="btn btn-outline-danger btn-sm btn-block"
                                                                        onClick={() => rollbackActions(item[removeItems[0]], item[removeItems[1]])}
                                                                        hidden={!removeActions ? false : true}
                                                                    >
                                                                        <i className="bi bi-arrow-counterclockwise"></i>
                                                                    </button>
                                                                )}
                                                            </div>
                                                        )
                                                    ) : (
                                                        editableIndex === index ? (
                                                            <input
                                                                type="text"
                                                                value={editedItems[cellIndex]}
                                                                onChange={(e) => setEditedItems[cellIndex](e.target.value)}
                                                            />
                                                        ) : (
                                                            (Array.isArray(heading.props.children) ? heading.props.children[0] : heading.props.children).includes("Status") ? (
                                                                (Array.isArray(heading.props.children) ? item[heading.props.children[0]] : item[heading.props.children]) === "Running" ? (
                                                                    <span className="bi-play-circle" style={{ color: "#FFC000" }}> Running</span>
                                                                ) : (Array.isArray(heading.props.children) ? item[heading.props.children[0]] : item[heading.props.children]) === "Success" ? (
                                                                    <span className="bi-check-circle" style={{ color: "green" }}> Success</span>
                                                                ) : (Array.isArray(heading.props.children) ? item[heading.props.children[0]] : item[heading.props.children]) === "Failed" ? (
                                                                    <span className="bi-x-circle" style={{ color: "red" }}> Failed</span>
                                                                ) : null
                                                            ) : (Array.isArray(heading.props.children) ? heading.props.children[0] : heading.props.children).includes("Time") ||
                                                                (Array.isArray(heading.props.children) ? heading.props.children[0] : heading.props.children).includes("INCR") ? (
                                                                (() => {
                                                                    const date = new Date(Array.isArray(heading.props.children) ? item[heading.props.children[0]] : item[heading.props.children]);
                                                                    const shortOptions = {
                                                                        year: 'numeric',
                                                                        month: '2-digit',
                                                                        day: '2-digit'
                                                                    };
                                                                    const options = {
                                                                        year: 'numeric',
                                                                        month: '2-digit',
                                                                        day: '2-digit',
                                                                        hour: '2-digit',
                                                                        minute: '2-digit',
                                                                        second: '2-digit',
                                                                        hour12: true
                                                                    };
                                                                    const shortDateString = date.toLocaleString('en-US', shortOptions);
                                                                    const fullDateString = date.toLocaleString('en-US', options);
                                                                    const fullDate = date.getTime() !== 0 ? fullDateString : '';
                                                                    const shortDate = date.getTime() !== 0 ? shortDateString : '';
                                                                    return (
                                                                        <span title={fullDate}>
                                                                            {shortDate ? shortDate + '...' : null}
                                                                        </span>
                                                                    );
                                                                })()

                                                            ) : (
                                                                (heading.props.children[0] === "Profile Name" || heading.props.children[0] === "Output Table Name" || heading.props.children[0] === "Run ID" || heading.props.children[0] === "Configuring File" || heading.props.children[0] === "Comparison Summary") ? (
                                                                    (heading.props.children[0] === "Configuring File" || heading.props.children[0] === "Comparison Summary") ? (
                                                                        <Button variant="outline-primary btn-sm btn-block" onClick={() => {
                                                                            const getData = displayData[heading.props.children[0]];
                                                                            heading.props.children[0] === "Configuring File"
                                                                                ? getData(item["rf_runid"])
                                                                                : getData(item["rf_runid"], item["Output Table Name"]);
                                                                            setSelectedId('');
                                                                            setSelectedId(item["id"]); // Update selectedItemId when the button is clicked
                                                                        }}>
                                                                            {heading.props.children[0] === "Configuring File" ? (
                                                                                iscfLoading && item["id"] === selectedId ? (
                                                                                    // Display the spinner when loading is true
                                                                                    <ScaleLoader height={10} width={5} color={"#2E67F8"} loading={iscfLoading} />
                                                                                ) : (
                                                                                    "View"
                                                                                )
                                                                            ) : (
                                                                                heading.props.children[0] === "Comparison Summary" ? (
                                                                                    iscsLoading && item["id"] === selectedId ? (
                                                                                        // Display the spinner when loading is true
                                                                                        <ScaleLoader height={10} width={5} color={"#2E67F8"} loading={iscsLoading} />
                                                                                    ) : (
                                                                                        "View"
                                                                                    )
                                                                                ) : null // If neither "Configuring File" nor "Comparison Summary", render nothing
                                                                            )}
                                                                        </Button>
                                                                    ) : (
                                                                        (heading.props.children[0] === "Output Table Name") ? (
                                                                            (item[heading.props.children[0]]) ? (
                                                                                <Button variant="outline-primary btn-sm btn-block" onClick={() => {
                                                                                    const getData = displayData[heading.props.children[0]];
                                                                                    getData(item["Output Table Name"]);
                                                                                    setSelectedId('');
                                                                                    setSelectedId(item["id"]);
                                                                                }}>
                                                                                    {item[heading.props.children[0]].split('.').pop().replace(/[\d-]/g, '')}
                                                                                </Button>
                                                                            ) : (
                                                                                <span></span>
                                                                            )
                                                                        ) : (
                                                                            <Button variant="outline-primary btn-sm btn-block" onClick={() => {
                                                                                const getData = displayData[heading.props.children[0]];
                                                                                heading.props.children[0] === "Run ID"
                                                                                    ? getData(item[heading.props.children[0]])
                                                                                    : getData(item["Profile Name"]);
                                                                                setSelectedId('');
                                                                                setSelectedId(item["id"]); // Update selectedItemId when the button is clicked
                                                                            }}>

                                                                                {heading.props.children[0] === "Profile Name" ? (
                                                                                    isgrdqLoading && item["id"] === selectedId ? (
                                                                                        // Display the spinner when loading is true
                                                                                        <ScaleLoader height={10} width={5} color={"#2E67F8"} loading={isgrdqLoading} />
                                                                                    ) : (
                                                                                        item[heading.props.children[0]]
                                                                                    )
                                                                                ) : (
                                                                                    item[heading.props.children[0]]
                                                                                )}

                                                                            </Button>

                                                                        )
                                                                    )

                                                                ) : (
                                                                    item[heading.props.children]
                                                                )
                                                            )
                                                        )
                                                    )}
                                                </td>
                                            ))}

                                        </tr>
                                    ))}

                                </tbody>
                            ) : (
                                <tbody>
                                    <tr>
                                        <td colSpan="8" className="text-center" style={{ padding: '20px', backgroundColor: '#f8f9fa', color: '#6c757d', fontWeight: 'bold' }}>
                                            No Data Available
                                        </td>
                                    </tr>
                                </tbody>
                            )}
                        </table>
                        <Pagination className="justify-content-end pagination-sm">
                            <Pagination.Prev onClick={prevPage} disabled={currentPage === 1} />
                            {Array.from({ length: endPage - startPage + 1 }, (_, index) => (
                                <Pagination.Item
                                    key={startPage + index}
                                    active={currentPage === startPage + index}
                                    onClick={() => currPage(startPage + index)}
                                >
                                    {startPage + index}
                                </Pagination.Item>
                            ))}
                            <Pagination.Next onClick={nxtPage} disabled={currentPage === totalPages} />
                        </Pagination>
                    </div>
                </div>
            )}
        </div>
    );
}

export default DataTable;