import React, { createContext, useContext, useState, useEffect } from 'react';

const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [username, setUsername] = useState(() => localStorage.getItem('username') || '');
  const [id, setId] = useState(() => localStorage.getItem('id') || '');
  const [usertype, setUserType] = useState(() => localStorage.getItem('usertype') || '');
  const [firstname, setFirstName] = useState(() => localStorage.getItem('firstname') || '');
  const [lastname, setLastName] = useState(() => localStorage.getItem('lastname') || '');

  const login = (username, id ,usertype, firstname, lastname) => {
    // Perform login logic, set username in the state and localStorage
    setUsername(username);
    setId(id);
    setUserType(usertype);
    setFirstName(firstname);
    setLastName(lastname);
    localStorage.setItem('username', username);
    localStorage.setItem('id', id);
    localStorage.setItem('usertype', usertype);
    localStorage.setItem('firstname', firstname);
    localStorage.setItem('lastname', lastname);
  };

  const logout = () => {
    // Perform logout logic, reset username in the state and localStorage
    setUsername('');
    setId('');
    setUserType('');
    setFirstName('');
    setLastName('');
    localStorage.removeItem('username');
    localStorage.removeItem('id');
    localStorage.removeItem('usertype');
    localStorage.removeItem('firstname');
    localStorage.removeItem('lastname');
  };

  // Use useEffect to check localStorage on component mount
  useEffect(() => {
    const storedUsername = localStorage.getItem('username');
    const storedId = localStorage.getItem('id');
    const storedUserType = localStorage.getItem('usertype');
    const storedfirstname = localStorage.getItem('firstname');
    const storedlastname = localStorage.getItem('lastname');
    if (storedUsername && storedId && storedUserType) {
      setUsername(storedUsername);
      setId(storedId);
      setUserType(storedUserType);
      setFirstName(storedfirstname);
      setLastName(storedlastname);
    }
  }, []);

  return (
    <AuthContext.Provider value={{ username, id, usertype, firstname, lastname, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};



export const useAuth = () => {
  return useContext(AuthContext);
};
