import React, { useState } from 'react';
import Header from "./Header";

function Configuration() {


    return (
        <div>
            <Header></Header>
            <div className="mt-5 p-5">
                <div className='row'>
                    <div className='col-sm-2'></div>
                    <div className='col-sm-6'>
                        <div class="container-sm border">
                        <div>
                      <div class="row mt-2">
                        <h6 class="text-center display-7"><small>Configuration Detail</small></h6>
                      </div>
                      <hr />
                    </div>
                        <div className='col-sm-12'>
                            <div className='row'>
                                <div className='col-sm-6'>
                                    <div className="form-group form-control-sm">
                                        <label htmlFor="developmentInput" className="form-label">Development</label>
                                        <input type="text" className="form-control" id="developmentInput" value={process.env.REACT_APP_development} readOnly />
                                    </div>
                                </div>                             
                            </div>                         
                            <div className='row'>
                                <div className='col-sm-6'>
                                    <div className="form-group form-control-sm">
                                        <label htmlFor="databricksUrlInput" className="form-label">Databricks URL</label>
                                        <input type="text" className="form-control" id="databricksUrlInput" value={process.env.REACT_APP_databricks_url} readOnly />
                                    </div>
                                </div>

                                <div className='col-sm-6'>
                                    <div className="form-group form-control-sm">
                                        <label htmlFor="bronzeJobIdInput" className="form-label">Bronze Job ID</label>
                                        <input type="text" className="form-control" id="bronzeJobIdInput" value={process.env.REACT_APP_bronze_jobid} readOnly />
                                    </div>
                                </div>
                            </div>
                            <div className='row'>
                                <div className='col-sm-6'>
                                    <div className="form-group form-control-sm">
                                        <label htmlFor="silverJobIdInput" className="form-label">Silver Job ID</label>
                                        <input type="text" className="form-control" id="silverJobIdInput" value={process.env.REACT_APP_silver_jobid} readOnly />
                                    </div>
                                </div>

                                <div className='col-sm-6'>
                                    <div className="form-group form-control-sm">
                                        <label htmlFor="dataQualityJobIdInput" className="form-label">Data Quality Job ID</label>
                                        <input type="text" className="form-control" id="dataQualityJobIdInput" value={process.env.REACT_APP_dataquality_jobid} readOnly />
                                    </div>
                                </div>
                            </div>
                            </div>
                        </div>
                    </div>

                    <div className='col-sm-2'></div>
                </div>
            </div>
        </div>
    );
}

export default Configuration;
