import { ScaleLoader } from 'react-spinners';

const AlertModel = ({
    modelTitle,
    lblName,
    showModal,
    closeModal,
    isalLoading,
    saveClick,
    btnname,
    btncolor,
    btnclass
}) => {
    return (
        <div id="Popalert" className={`modal fade ${showModal ? 'show' : ''}`} role="dialog" style={{ display: showModal ? 'block' : 'none' }}>
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <h4 className="modal-title">{modelTitle}</h4>
                        <button type="button" className="btn-close" data-bs-dismiss="modal" data-dismiss="modal" onClick={closeModal}></button>
                    </div>
                    <div className="modal-body ">
                        <div className="container-sm">
                            <div className="row">
                                <div className="col-sm-12">
                                    <label id="lblalertname">Are you sure you want to {lblName}</label>
                                    <span id="Spanalmsg"></span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="modal-footer">
                        <div className="form-group form-control-sm d-grid">
                            <button
                                type="button"
                                className={btnclass}
                                onClick={saveClick}
                            >
                                {isalLoading ? (
                                    <ScaleLoader height={10} width={5} color={btncolor} loading={isalLoading} />
                                ) : (
                                    btnname
                                )}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default AlertModel;
