import { ScaleLoader, PuffLoader } from 'react-spinners';

const CheckNameModel = ({
    modelTitle,
    lblName,
    lblInfo,
    showModal,
    closeModal,
    ispValid,
    ispsvLoading,
    ispcnLoading,
    changeName,
    setchangeName,
    popnameFocused,
    setPopNameFocused,
    checkName,
    clear,
    saveClick,
    isNameExists
}) => {
    return (
        <div id="Popchangedbname" className={`modal fade ${showModal ? 'show' : ''}`} role="dialog" style={{ display: showModal ? 'block' : 'none' }}>
            <div className="modal-dialog">
                <div className="modal-content">

                    <div className="modal-header">
                        <h4 className="modal-title">{modelTitle}</h4>
                        <button type="button" className="btn-close" data-bs-dismiss="modal" data-dismiss="modal" onClick={closeModal}></button>
                    </div>

                    <div className="modal-body ">

                        <div className="container-sm border">

                            <div className="row mt-2">
                                <div className="col-sm-1"></div>
                                <div className="col-sm-10">
                                    <div id="dvdatabasenamemodal" className="form-group form-control-sm">
                                        <div className="text-input">
                                            <label className={`${ispValid ? '' : 'is-invalid'} ${popnameFocused || changeName !== "" ? "label-focus" : ""}`}>
                                                {lblName} <span className="bi bi-info-circle" data-bs-placement="top" data-bs-toggle="tooltip" title={lblInfo}></span>
                                            </label>
                                            {ispcnLoading ? (
                                                <div style={{ position: 'absolute', top: '50%', right: '8px', transform: 'translateY(-50%)' }}>
                                                    <PuffLoader size={21} color={"#2E67F8"} loading={ispcnLoading} />
                                                </div>
                                            ) : (
                                                null
                                            )}
                                            <input
                                                type="text"
                                                id="txtdatabasename"
                                                className={`form-control form-control-sm ${ispValid ? '' : 'is-invalid'} ${popnameFocused || changeName !== "" ? "input-focus" : ""}`}
                                                value={changeName}
                                                onChange={(e) => setchangeName(e.target.value)}
                                                onFocus={() => setPopNameFocused(true)}
                                                onBlur={(event) => {
                                                    checkName(event);
                                                    setPopNameFocused(false);
                                                }}
                                            />

                                        </div>
                                    </div>
                                </div>
                                <div class="col-sm-1 d-flex align-items-center">

                                </div>
                            </div>
                            <div className="row">
                                <div className="col-sm-1"></div>
                                <div className="col-sm-10">
                                    <div className="form-group form-control-sm">
                                        <div className='d-flex justify-content-end'>
                                            <button
                                                type="button"
                                                className="btn btn-outline-primary btn-sm btn-block me-2"
                                                onClick={clear}
                                            >
                                                Clear
                                            </button>
                                            <button
                                                type="button"
                                                className="btn btn-outline-primary btn-sm btn-block"
                                                onClick={saveClick}
                                                disabled={isNameExists}
                                            >
                                                {ispsvLoading ? (
                                                    <ScaleLoader height={10} width={5} color={"#2E67F8"} loading={ispsvLoading} />
                                                ) : (
                                                    "Save"
                                                )}
                                            </button>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-sm-1">
                                </div>
                            </div>
                            <div className="p-1"></div>
                        </div>
                    </div>
                </div>
                <div className="modal-footer">
                </div>
            </div>
        </div>
    );
}

export default CheckNameModel;
