import React, { useRef, useEffect, useState } from "react";
import ReactSelect, { components } from "react-select";
import { PuffLoader } from "react-spinners";

export const MultiSelect = (props) => {
  const valueRef = useRef(props.value);
  valueRef.current = props.value;
  const [menuWidth, setMenuWidth] = useState("auto");
  const [isFocused, setIsFocused] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false); // New state to control menu open/close

  const { MultiValueRemove } = components;

  const CustomMultiValueRemove = (props) => {
    return null; // Return null to hide the remove button
  };

  const CustomInput = (props) => {
    return null; // Return null to hide the input
  };

  useEffect(() => {
    const handleDocumentClick = (e) => {
      if (!e.target.closest(".multi-select-container")) {
        setIsMenuOpen(false);
      }
    };

    document.addEventListener("click", handleDocumentClick);

    return () => {
      document.removeEventListener("click", handleDocumentClick);
    };
  }, []);

  useEffect(() => {
    const longestLabel = props.options.reduce(
      (longest, option) =>
        option.label.length > longest ? option.label.length : longest,
      0
    );
    setMenuWidth(`${longestLabel * 8}px`);
  }, [props.options]);

  const selectAllOption = {
    value: "<SELECT_ALL>",
    label: "Select All",
  };

  const isSelectAllSelected = () =>
    valueRef.current.length === props.options.length;

  const isOptionSelected = (option) =>
    valueRef.current.some(({ value }) => value === option.value) ||
    isSelectAllSelected();

  const getOptions = () => [selectAllOption, ...props.options];

  const getValue = () => {
    const selectedItemsCount = valueRef.current.length;
    let labelDisplayed = false;

    if (selectedItemsCount === 0) {
      return [];
    } else if (isSelectAllSelected()) {
      return [{ value: selectAllOption.value, label: `${props.options.length} items selected` }];
    } else if (selectedItemsCount === 1) {
      // Display the name of the selected item if only one item is selected
      const selectedOption = valueRef.current[0];
      return [{ value: selectedOption.value, label: selectedOption.value }];
    } else {
      return valueRef.current.map(option => {
        if (!labelDisplayed) {
          labelDisplayed = true;
          return { value: option.value, label: `${selectedItemsCount} items selected` };
        } else {
          return { value: option.value };
        }
      });
    }
  };

  const onChange = (newValue, actionMeta) => {
    const { action, option, removedValue } = actionMeta;

    if (action === "select-option" && option.value === selectAllOption.value) {
      props.onChange(props.options, actionMeta);
    } else if (
      (action === "deselect-option" &&
        option.value === selectAllOption.value) ||
      (action === "remove-value" &&
        removedValue.value === selectAllOption.value)
    ) {
      props.onChange([], actionMeta);
    } else if (
      actionMeta.action === "deselect-option" &&
      isSelectAllSelected()
    ) {
      props.onChange(
        props.options.filter(({ value }) => value !== option.value),
        actionMeta
      );
    } else {
      props.onChange(newValue || [], actionMeta);
    }
  };

  return (
    <div className="multi-select-container" style={{ position: "relative" }}>
      <ReactSelect
        placeholder={isFocused || getValue().length > 0 ? "" : props.placeholder}
        isOptionSelected={isOptionSelected}
        options={getOptions()}
        value={getValue()}
        onChange={onChange}
        onBlur={() => setIsFocused(false)}
        onFocus={() => setIsFocused(true)}
        onMenuOpen={() => {
          if (props.options.length > 0) {
            setIsMenuOpen(true);
          }
        }} // Set the menu open state
        onMenuClose={() => setIsMenuOpen(false)} // Set the menu close state
        components={{
          MultiValueRemove: CustomMultiValueRemove,
          Input: CustomInput,
        }}
        className={props.className}
        hideSelectedOptions={false}
        closeMenuOnSelect={false}
        isMulti
        menuIsOpen={isMenuOpen && props.options.length > 0} // Open the menu when data is available and isMenuOpen is true
        styles={{
          multiValue: () => ({}), // Remove CSS for multi-value components
          multiValueRemove: () => ({}),
          menu: (provided) => ({
            ...provided,
            width: menuWidth,
            zIndex: 9999,
          }),
          option: (provided, state) => ({
            ...provided,
            backgroundColor: state.isSelected ? "#2E67F8" : "white",
            color: state.isSelected ? "white" : "black",
          }),
          control: (provided, state) => {
            const borderColor =
              state.selectProps.value.length > 0 ? "#2E67F8" : "#eee";
            return {
              ...provided,
              borderColor,
              borderWidth: "2px",
              borderStyle: "solid",
              borderRadius: "3px",
              outline: "none",
              boxShadow: "none",
              "&:hover": {
                borderColor: state.isFocused
                  ? provided.borderColor
                  : "#2E67F8",
              },
              "&:focus-within": {
                borderColor: "#2E67F8",
              },
              input: {
                visibility: "hidden"
              },
            };
          },
        }}
      />
      {props.isLoading && (
        <div
          style={{
            position: "absolute",
            top: "50%",
            right: "40px", // Adjust the position to the left side of the MultiSelect component
            transform: "translateY(-50%)",
          }}
        >
          <PuffLoader size={21} color={"#2E67F8"} loading={props.isLoading} />
        </div>
      )}
      {(isFocused || getValue().length > 0) && (
        <label
          style={{
            position: "absolute",
            top: "-10px",
            left: "10px",
            backgroundColor: "white",
            padding: "0 5px",
            color: "#2E67F8",
            zIndex: 1,
            fontWeight: "600",
            fontSize: "0.9em",
            userSelect: "none",
          }}
        >
          {props.placeholder}
        </label>
      )}
    </div>
  );
};
