import axios from 'axios';
import secureLocalStorage from 'react-secure-storage';

const devbaseURL = process.env.REACT_APP_devbaseURL;
const uatbaseURL = process.env.REACT_APP_uatbaseURL;
const localbaseURL = process.env.REACT_APP_localbaseURL;
//The timeout option in an Axios instance specifies the amount of time in milliseconds that the request should 
//wait for a response from the server before timing out.
// const instance = axios.create({
//   baseURL: `${uatbaseURL}`,
//   timeout: 500000,
// });
const instance = axios.create({
  baseURL: `${devbaseURL}`,
  timeout: 500000,
});
// const instance = axios.create({
//   baseURL: `${localbaseURL}`,
//   timeout: 500000,
// });

//The purpose of this code is to provide a centralized mechanism for adding authorization headers to HTTP 
//requests, so that you don't have to manually add the header to every request. Instead, the interceptor 
//takes care of adding the header for you, based on the presence of a token in local storage.
instance.interceptors.request.use(
  (config) => {
    const token = secureLocalStorage.getItem('Token');
    if (token) {
      config.headers['authorization'] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

export default instance;
