  async function RunProfile(name, buttonId) {
    let chk_token = await CheckToken();
    if (chk_token) {
      let profile_name = '';
      if (name === null) {
        setisrdqLoading(true);
        profile_name = selectedProfile;

      }
      else if (buttonId === "btnsave_run") {
        setissrLoading(true);
        profile_name = name;
      }
      else {
        setisgrdqLoading(true);
        profile_name = name;
      }
      const display_result = await axiosInstance.get("get_profile_detail", {
        params: {
          profile_name: profile_name,
        }
      });
      if (display_result.data.status) {
        const profileData = display_result.data.data[0];
        let Profile_json = `${JSON.stringify(profileData, (key, value) => {
          if (key === 'source_column' || key === 'target_column' || key === 'source_function' || key === 'target_function' || key === 'grain_columns') {
            return JSON.parse(value);
          }
          if (key === 'profile_id' || key === 'profile_name') {
            return undefined;
          }
          if (key === 'source_filter_condition') {
            return JSON.parse(value);
          }
          if (key === 'target_filter_condition') {
            return JSON.parse(value);
          }
          return value;
        }, 2)}`;

        Profile_json = Profile_json.replace("source_table", "source_table_name").replace("target_table", "target_table_name").replace("source_column", "source_column_name").replace("target_column", "target_column_name").replace("grain_columns", "grain_column").replace("source_connection_name", "source_connection").replace("target_connection_name", "target_connection");

        const profileObject = JSON.parse(Profile_json);
        profileObject.sample_row = 100;
        const Updated_Profile_json = JSON.stringify(profileObject, null, 2);

        const response = await axiosInstance.post("runjob", {
          configure_file: Updated_Profile_json,
          rf_runid: rf_run_id,
        });
        if (response) {
          const databricks_job_runid = response.data.response.run_id;
          const status = "Running"
          let configuration_name = profile_name;

          const json_value = Updated_Profile_json;

          const display_result = await axiosInstance.post("post_json_config", {
            json_value: json_value,
            status: status,
            rf_runid: rf_run_id,
            databricks_job_runid: databricks_job_runid,
            configuration_name: configuration_name,
          });
          if (display_result.data.status) {
            const re = display_result.data;
            const srow = 100;
            getdata(databricks_job_runid, rf_run_id, srow)
          }

        }
      }
    } else {
      navigate('/')
    }
  }

  const getdata = async (db_runid, rf_run_id, srow) => {
    let chk_token = await CheckToken();
    if (chk_token) {
      const display_result = await axiosInstance.get("get_job_status", {
        params: { db_runid: db_runid },
      });
      if (display_result.data) {
        const result = display_result.data;

        if (result.metadata.state.life_cycle_state === 'TERMINATED') {
          setResultStatus(result.metadata.state.result_state);

          if (result.metadata.state.result_state === 'SUCCESS') {
            const output = result.notebook_output.result;
            const outputObject = JSON.parse(output.replace(/'/g, '"'));
            const outputTable_Name = outputObject.output_table_name;

            if (outputTable_Name !== '') {
              const catalog_url = "https://exocube-dev-1.cloud.databricks.com/explore/data/catalogtablename?o=4167777371018860"

              const fullTableName = outputTable_Name.split('.');
              const outputCatalog = fullTableName[0];
              const outputSchemaName = fullTableName[1];
              const output_TableName = fullTableName[2];

              const createCatalogUrl = `${outputCatalog}/${outputSchemaName}/${output_TableName}`;

              const urlToReplace = 'catalogtablename';
              const urlReplacement = createCatalogUrl;

              const modifiedUrl = catalog_url.replace(new RegExp(urlToReplace, 'i'), urlReplacement);
              window.open(modifiedUrl, '_blank');
              setisrdqLoading(false);
              setisgrdqLoading(false);
              setissrLoading(false);
              setIsPanelOpen(false);
              setShowGridDetail(true);
              setShowTable(true);
              const e = {
                preventDefault: () => { }, // Mocking preventDefault for this example
                // Other properties/methods of the event object if needed
              };
              formClear(e);
              await dataquality_info();
            } else {

              await dataquality_info();
              toast.success("Run Successfully", { className: 'success-toast' });
              setIsPanelOpen(false);
              setShowGridDetail(true);
              setShowTable(true);
              const e = {
                preventDefault: () => { }, // Mocking preventDefault for this example
                // Other properties/methods of the event object if needed
              };
              formClear(e);

              var select = document.getElementById("ddprofilename");
              select.value = "";

              setisrdqLoading(false);
              setisgrdqLoading(false);
              setissrLoading(false);

            }
          } else {
            await dataquality_info();
            toast.error("Job failed", { className: 'error-toast' });
            navigate('/Dataquality')
            setIsPanelOpen(false);
            setShowGridDetail(true);
            setShowTable(true);
            const e = {
              preventDefault: () => { }, // Mocking preventDefault for this example
              // Other properties/methods of the event object if needed
            };
            formClear(e);
            setisrdqLoading(false);
            setissrLoading(false);
            setisgrdqLoading(false);
          }
        } else if (result.metadata.state.life_cycle_state === 'PENDING' || result.metadata.state.life_cycle_state === 'RUNNING') {
          setTimeout(() => {
            getdata(db_runid, rf_run_id, srow);
          }, 2000);
        }
      }
    } else {
      navigate('/')
    }
  };

