import React,{useState} from "react";
import '../CSS/sidebarmenu.css';
import { Link, useLocation } from "react-router-dom";

function Slidermenu() {
    const [isCollapsed, setIsCollapsed] = useState(false);
    let location = useLocation();

    const handleCollapseToggle = () => {
        setIsCollapsed(!isCollapsed);
    };
    return (
        <div>
            <div
                id="wrapper"
                className={`toggled-${isCollapsed ? 0 : 2} mt-5`}
            >
                <div id="sidebar-wrapper" class="bg-dark position-fixed">
                    <ul class="sidebar-nav nav-pills nav-stacked text-secondary" id="menu">
                        <li className={location.pathname == "/Datamigration_home" ? "nav-item" : "nav-item"}>
                            <Link to="/Datamigration_home" className={location.pathname == "/Datamigration_home" ? "nav-link2 active" : "nav-link2"}><span class="fa-stack pull-left"><i style={{ fontSize: '23px' }} class="fa fa-home fa-stack-1x"></i></span>&nbsp;Home</Link>
                        </li>
                        <li className={location.pathname == "/Datamigration_metadata" ? "nav-item" : "nav-item"}>
                            <Link to="/Datamigration_metadata" className={location.pathname == "/Datamigration_metadata" ? "nav-link2 active" : "nav-link2"}><span class="fa-stack pull-left"><i class="fa fa-connectdevelop fa-stack-1x" ></i></span>&nbsp;Table Metadata</Link>
                        </li>
                        <li className={location.pathname == "/Datamigration_migratetable" && location.pathname == "/Datamigration_home" ? "nav-item" : "nav-item"}>
                            <Link to="/Datamigration_migratetable" className={location.pathname == "/Datamigration_migratetable" ? "nav-link2 active" : "nav-link2"}><span class="fa-stack pull-left"><i class="fa fa-exchange fa-stack-1x "></i></span>&nbsp;Migrate Table</Link>
                        </li>
                    </ul>
                    <ul className="sidebar-menu-footer nav-pills nav-stacked">
                        <li>
                            <a
                                onClick={handleCollapseToggle}
                                data-toggle="collapse"
                                id="menu-toggle-2"
                            >
                                <span className="fa-stack pull-left">
                                    <i className="fa fa-bars link-light fa-stack-1x"></i>
                                </span>&nbsp;Collapse Menu
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    );
}

export default Slidermenu;