  async function bronzegetdata(bronze_table_name, run_id) {
    let chk_token = await CheckToken();
    if (chk_token) {
      const response = await axiosInstance.get("get_job_status", {
        params: {
          db_runid: run_id,
        }
      });
      if (response) {
        const bronze_result_status = response?.data?.metadata?.state.result_state;

        const jobstatus = response?.data?.metadata?.state?.life_cycle_state;

        const bronze_runid = response?.data?.metadata?.run_id;

        const param = response?.data?.metadata?.overriding_parameters.notebook_params.bronze_table_name;

        const jobstartunixtimestamp = response?.data?.metadata?.start_time;
        const jobendunixtimestamp = response?.data?.metadata?.end_time;

        const jobStartTime = new Date(0);
        jobStartTime.setUTCSeconds(Math.round(parseInt(jobstartunixtimestamp) / 1000));

        const jobEndTime = new Date(0);
        jobEndTime.setUTCSeconds(Math.round(parseInt(jobendunixtimestamp) / 1000));

        const joburl = response?.data?.metadata?.run_page_url;

        if (bronzesJsonTables[bronze_table_name]) {
          bronzesJsonTables[bronze_table_name].start_time = jobStartTime;
          bronzesJsonTables[bronze_table_name].status = jobstatus;
          bronzesJsonTables[bronze_table_name].URL = joburl;
          bronzesJsonTables[bronze_table_name].end_time = jobEndTime;
        }
        else {
          console.error(`Table with name '${bronze_table_name}' not found.`);
        }
        const bronzeupdate = secureLocalStorage.getItem('bronze_json');
        if (bronzeupdate[bronze_table_name]) {
          bronzeupdate[bronze_table_name].start_time = jobStartTime;
          bronzeupdate[bronze_table_name].URL = joburl;
          bronzeupdate[bronze_table_name].end_time = jobEndTime;
          setbronzeurl(joburl);
        }
        
        switch (jobstatus) {
          case 'PENDING':
            setisstLoading((prevIsstLoading) => ({ ...prevIsstLoading, [`bronze_${param}`]: true }));
            setColorsStatus((prevColorsStatus) => ({ ...prevColorsStatus, [`bronze_${param}`]: 'bg-secondary' }));
            const prun_url = `${process.env.REACT_APP_databricks_url}/${process.env.REACT_APP_bronze_jobid}/run/${bronze_runid}`;
            setbronzeurl(prun_url);

            const tid = "bronze_title_" + param;
            const element = document.getElementById(tid);
            if (element) {
              element.style.display = 'none'; // Set display to 'block' to show the element
            }

            setTimeout(() => bronzegetdata(param, bronze_runid), 2000);

            const pid = "bronzeLink_" + param;
            const pelement = document.getElementById(pid);
            if (pelement) {
              pelement.style.display = 'block'; // Set display to 'none' to hide the element
            }
            break;
          case 'RUNNING':
            setisstLoading((prevIsstLoading) => ({ ...prevIsstLoading, [`bronze_${param}`]: true }));
            setColorsStatus((prevColorsStatus) => ({ ...prevColorsStatus, [`bronze_${param}`]: 'bg-warning' }));
            const rrun_url = `${process.env.REACT_APP_databricks_url}/${process.env.REACT_APP_bronze_jobid}/run/${bronze_runid}`;
            setbronzeurl(rrun_url);
            const rid = "bronze_title_" + param;
            const relement = document.getElementById(rid);
            if (relement) {
              relement.style.display = 'none'; // Set display to 'block' to show the element
            }

            setTimeout(() => bronzegetdata(param, bronze_runid), 2000);

            const rpid = "bronzeLink_" + param;
            const rpelement = document.getElementById(rpid);
            if (rpelement) {
              rpelement.style.display = 'block'; // Set display to 'none' to hide the element
            }
            break;
          case 'TERMINATED':
            if (bronzesJsonTables[param]) {
              bronzesJsonTables[param].status = bronze_result_status;
              bronzeupdate[param].status = bronze_result_status;
            }
            switch (bronze_result_status) {
              case 'SUCCESS':
                setisstLoading((prevIsstLoading) => ({ ...prevIsstLoading, [`bronze_${param}`]: false }));
                setColorsStatus((prevColorsStatus) => ({ ...prevColorsStatus, [`bronze_${param}`]: 'bg-success' }));
                const srun_url = `${process.env.REACT_APP_databricks_url}/${process.env.REACT_APP_bronze_jobid}/run/${bronze_runid}`;
                setbronzeurl(srun_url);
                await triggersilver(param);

                break;
              case 'FAILED':
                setisstLoading((prevIsstLoading) => ({ ...prevIsstLoading, [`bronze_${param}`]: false }));
                setColorsStatus((prevColorsStatus) => ({ ...prevColorsStatus, [`bronze_${param}`]: 'bg-danger' }));
                const frun_url = `${process.env.REACT_APP_databricks_url}/${process.env.REACT_APP_bronze_jobid}/run/${bronze_runid}`;
                setbronzeurl(frun_url);

                break;
              default:
                // Handle other cases if needed
                break;
            }
            break;
          case 'SKIPPED':
            setColorsStatus((prevColorsStatus) => ({ ...prevColorsStatus, [`bronze_${param}`]: 'bg-secondary' }));
            break;
          default:
            setColorsStatus((prevColorsStatus) => ({ ...prevColorsStatus, [`bronze_${param}`]: 'bg-light' }));
            break;
        }
        secureLocalStorage.setItem('bronze_json', bronzeupdate);
      }
    } else {
      navigate('/')
    }
  }

