                setSilverColorsStatus((prevsilverColorsStatus) => ({ ...prevsilverColorsStatus, [`silver_${param}`]: 'bg-success' }));
                const srun_url = `${process.env.REACT_APP_databricks_url}/${process.env.REACT_APP_silver_jobid}/run/${silver_runid}`;
