  function calculateRuntime(jobendtime, jobstarttime) {
    const durationMilliseconds = jobendtime - jobstarttime;
    const durationSeconds = Math.floor(durationMilliseconds / 1000);

    const hours = Math.floor(durationSeconds / 3600);
    const minutes = Math.floor((durationSeconds % 3600) / 60);
    const seconds = durationSeconds % 60;

    let total_run_time = '';

    if (hours !== 0) {
      total_run_time += hours + 'h ';
    }
    if (minutes !== 0) {
      total_run_time += minutes + 'm ';
    }
    if (seconds !== 0 || (hours === 0 && minutes === 0)) {
      total_run_time += seconds + 's';
    }

    return total_run_time;
  }

