  const get_cluster_status = async (cluster_id, job_name) => {
    let chk_token = await CheckToken();
    if (chk_token) {
      const display_results = await axiosInstance.get("get_cluster_detail", {
        params: {
          cluster_id: cluster_id,
        }
      });
      if (display_results.data) {
        const json_value = display_results.data;
        if (json_value.state === "PENDING") {
          setTimeout(() => {
            get_cluster_status(cluster_id, job_name);
          }, 2000);
        }
        else if (json_value.state === "RUNNING") {
          if(job_name==="Bronze")
          {
            openModal_Popbronzeswapcluster();
          }
          else if(job_name==="Silver")
          {
            openModal_Popsilverswapcluster(); 
          }
          else if (job_name==="BronzeStart"){         
              const response = await axiosInstance.post("post_swap_cluster", {
                job_id: process.env.REACT_APP_bronze_jobid,
                cluster_id: cluster_id,
              });
              if (response) {
                bronze_existing_job_id_details();
                toast.success('Change Cluster Successfully', { className: 'success-toast' });
                closeModal_Popstcluster();
                setSCSLoading(false);
                await bronze_cluster_list();
                await silver_cluster_list();
              }
          }
          else if(job_name==="SilverStart"){
            const response = await axiosInstance.post("post_swap_cluster", {
              job_id: process.env.REACT_APP_silver_jobid,
              cluster_id: cluster_id,
            });
            if (response) {
              silver_existing_job_id_details();
              toast.success('Change Cluster Successfully', { className: 'success-toast' });
              closeModal_Popstcluster();            
              setSCSLoading(false);
              await bronze_cluster_list();
              await silver_cluster_list();
            }
          }             
        }
        else if (json_value.state === "TERMINATED") {
          console.log(cluster_id, job_name)
          openModal_Popstcluster();
        }
      } else {
        //console.error("Data is not an array:", display_results.data);
      }
    } else {
      navigate('/')
    }
  }

